import { faBell, faPowerOff } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import logo from '../../assets/imgs/badge.png';
import { confirm } from '../../common/Utils';
import { AppContext } from '../../context/AppContext';

class Toolbar extends Component {
  static contextType = AppContext;

  constructor(props) {
    super(props);
    this.state = {};
  }

  logOutUser = () => {
    confirm('Are you sure you want to logout?', () => {
      this.context.auth.AuthLogout();
      try {
        this.props.props.props.history.push('/login');
      } catch (e) {
        this.props.props.history.push('/login');
      }
    }, () => { })
  }

  render() {
    return (
      <>

        <div style={{ zIndex: 1 }} className='w-100 h-auto position-relative'>
          <div id="notify" className="position-relative w-100">
            {/* Alert */}
            <div id="alertContainer" style={{ zIndex: 2070 }} className="toast-container position-absolute top-0 end-0 mt-3 me-lg -3 me-2"></div>
          </div>
        </div>

        {/* Intro */}
        <header>
          <div className="">

            <nav className="navbar shadow-sm navbar-expand-lg navbar-light px-lg-3 bg-white">
              <div className="container px-2">
                <Link className="navbar-brand" to="/">
                  <img src={logo} width={30} alt="" />
                  <span className='fw-bold ms-2 d-none d-md-inline-block text-primary align-middle'>SPORT FANS CLUB</span>
                </Link>
                <div className="" id="navbarTogglerDemo02">

                  <div className="ms-auto d-flex align-middle">
                    <Link to={this.context.auth.role === 'admin' ? '/admin/notifications' : '/member/notifications'} className="btn btn-muted fs-5 text-primary">
                      {/* <div className="btn btn-muted fs-5 text-primary"> */}
                      <FontAwesomeIcon className={this.context.auth.user.notifications > 0 ? 'text-primary' : 'text-secondary'} icon={faBell} ></FontAwesomeIcon>
                      {/* </div> */}
                    </Link>
                    <button onClick={this.logOutUser} className="btn btn-muted ms-1 fs-5">
                      <FontAwesomeIcon icon={faPowerOff} className='text-danger'></FontAwesomeIcon>
                      <span>&nbsp;Logout</span>
                    </button>

                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#sidebar" aria-controls="sidebar" aria-expanded="false" aria-label="Toggle navigation">
                      <span className="navbar-toggler-icon" />
                    </button>

                    {/* <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navba rTogglerDemo02" aria-controls="navbar TogglerDemo02" aria-expanded="false" aria-label="Toggle navigation">
                      <span className="navbar-toggler-icon" />
                      <span className="navbar-toggler-icon" />
                    </button> */}
                  </div>
                </div>
              </div>
              {/* <div className='clearfix'></div> */}
            </nav>

          </div>
        </header>


      </>

    );
  }

}

export default Toolbar;