import React, { Component } from 'react';
import { AppProvider, Auth } from './context/AppContext';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './assets/styles/admin_dashboard.css'
import Home from './home/Home';
import AboutUs from './aboutus/AboutUs';
import HowItWorks from './howitworks/HowItWorks';
import Benefits from './benefits/Benefits';
import TermsAndConditions from './termsandconditions/TermsAndConditions';
import Faq from './faq/Faq';
import ContactUs from './contactus/ContactUs';
import $404 from './404/404';
import Register from './register/Register';
import Login from './login/Login';
import ForgotPassword from './forgotpassword/ForgotPassword';
import MemberDashboard from './members/dashboard/Dashboard';
import MemberProfile from './members/profile/Profile';
import MemberSlots from './members/slots/Slots';
import MemberDownlines from './members/downlines/Downlines';
import MemberBenefits from './members/benefits/Benefits';
import MemberNotifications from './members/notifications/Notifications';
import AdminCompetitions from './admin/competitions/AdminCompetitions';
import AdminUsers from './admin/users/Users';
import AdminInvoices from './admin/invoices/Invoices';
import AdminAllDownlines from './admin/downlines/Downlines';
import AdminReferralLevels from './admin/referrallevels/ReferralLevels';
import AdminSlotPackges from './admin/slotpackages/SlotPackges';
import PayInvoice from './members/payinvoice/PayInvoice';
import AdminUserProfile from './admin/users/UserProfile';
import AdminNotifications from './admin/notifications/Notifications';
import AdminMemberBenefits from './admin/memberbenefits/Benefits';
import AdminMemberSlots from './admin/memberslots/Slots';
import AdminPayments from './admin/payments/Payments';
import Verify from './members/verify/Verify';
import PrivacyPolicy from './termsandconditions/PrivacyPolicy';
// import configureStore from './redux/configureStore';
// import { Provider } from 'react-redux';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      auth: new Auth()
    }
    // console.log('constructor');
  }
  
  render() {
    console.log('rerendering...')
    const { auth } = this.state;
    this.state.auth.persistAuth();
    
    // // using redux
    // const store = configureStore();

    return (
      <>

        {/* Modal */}
        <div>
          {/* Button trigger modal */}
          <button id="confirmModalTrigger" type="button" className="d-none" data-bs-toggle="modal" data-bs-target="#confirmModal"></button>
          <div className="modal fade" id="confirmModal" tabIndex={-1} aria-hidden="true" data-bs-backdrop="static">
            <div className="modal-dialog">
              <button id="confirmModalDismissTrigger" type="button" className="d-none btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
              <div id="confirmModalContent" className="modal-content"></div>
            </div>
          </div>
        </div>

        {/* <Provider store={store}> */}
          <AppProvider value={{ auth }}>

            <Router>
              <Switch>

                {/* Public Routes */}
                <Route exact path='/' component={Home}></Route>
                <Route exact path='/about' component={AboutUs}></Route>
                <Route exact path='/benefits' component={Benefits}></Route>
                <Route exact path='/how' component={HowItWorks}></Route>
                <Route exact path='/terms' component={TermsAndConditions}></Route>
                <Route exact path='/privacy' component={PrivacyPolicy}></Route>
                <Route exact path='/faqs' component={Faq}></Route>
                <Route exact path='/contact' component={ContactUs}></Route>
                <Route exact path='/register' component={Register}></Route>
                <Route exact path='/login' component={Login}></Route>
                <Route exact path='/forgot' component={ForgotPassword}></Route>
                {/* <Route exact path='/reset' component={ResetPassword}></Route> */}

                {/* Member Authenticated Routes */}
                <Route exact path='/member/dashboard' component={MemberDashboard}></Route>
                <Route exact path='/member/profile' component={MemberProfile}></Route>
                <Route exact path='/member/slots' component={MemberSlots}></Route>
                <Route exact path='/member/downlines' component={MemberDownlines}></Route>
                <Route exact path='/member/benefits' component={MemberBenefits}></Route>
                <Route exact path='/member/notifications' component={MemberNotifications}></Route>
                <Route exact path='/member/invoice' component={PayInvoice}></Route>
                <Route exact path='/member/verify' component={Verify}></Route>

                {/* Admin Authenticated Routes */}
                {/* <Route exact path='/admin/dashboard' component={auth.AuthGuard('admin') === true ? MemberDashboard : () => auth.AuthRedirect('/login')}></Route> */}
                <Route exact path='/admin/dashboard' component={MemberDashboard}></Route>
                <Route exact path='/admin/profile' component={MemberProfile}></Route>
                <Route exact path='/admin/users' component={AdminUsers}></Route>
                <Route exact path='/admin/users/profile' component={AdminUserProfile}></Route>
                <Route exact path='/admin/invoices' component={AdminInvoices}></Route>
                <Route exact path='/admin/competitions' component={AdminCompetitions}></Route>
                <Route exact path='/admin/slots' component={MemberSlots}></Route>
                <Route exact path='/admin/downlines' component={MemberDownlines}></Route>
                <Route exact path='/admin/benefits' component={MemberBenefits}></Route>
                <Route exact path='/admin/users/downlines' component={AdminAllDownlines}></Route>
                <Route exact path='/admin/referrallevels' component={AdminReferralLevels}></Route>
                <Route exact path='/admin/slotpackages' component={AdminSlotPackges}></Route>
                <Route exact path='/admin/notifications' component={MemberNotifications}></Route>
                <Route exact path='/admin/invoice' component={PayInvoice}></Route>
                <Route exact path='/admin/verify' component={Verify}></Route>
                <Route exact path='/admin/payments' component={AdminPayments}></Route>
                <Route exact path='/admin/users/benefits' component={AdminMemberBenefits}></Route>
                <Route exact path='/admin/users/slots' component={AdminMemberSlots}></Route>
                <Route exact path='/admin/admin/notifications' component={AdminNotifications}></Route>

                {/* Fallback Route */}
                <Route component={$404}></Route>

              </Switch>
            </Router>

          </AppProvider>
        {/* </Provider> */}

      </>
    );
  }

}

export default App;
