import React, { Component } from 'react';
import { Link } from 'react-router-dom';


// images
import Header from '../header/Header';
import Footer from '../footer/Footer';
import { query, request, alert } from '../common/Utils';
import logo from '../assets/imgs/badge.png';
import { AppContext } from '../context/AppContext';


class Login extends Component {
  static contextType = AppContext;

  state = {
    isSubmitting: false,
    showPassword: false,
    showConfirmPassword: false,
    slots: [],
    queryMap: {}
  }

  submitLogin = async (e) => {
    e.preventDefault();

    this.setState({ isSubmitting: true })
    let email = query('#loginEmail')[0].value;
    let password = query('#loginPassword')[0].value;

    let body = new FormData();
    body.append('email', email);
    body.append('password', password);

    try {
      let resp = await request('/api/login', 'POST', body);

      if (resp.status === true) {
        alert(`Welcome back ${email}`, 'success');
        await this.context.auth.AuthLogin(resp.data.token, email, resp.data.role, resp.data.permissions ?? []);
        // await this.context.auth.user.persistUser(this.context.auth);

        // get user's profile
        let headers = new Headers();
        headers.append('AuthToken', resp.data.token);
        let uresp = await request('/api/profile', 'GET', null, headers);
        if (uresp.status === true) {
          this.context.auth.setUser(uresp.data);
        } else {
          // this.context.auth.AuthLogout();
          alert('Invalid user', 'error');
        }

        setTimeout(() => {
          if (resp.data.role === 'member') {
            this.props.history.push('/member/dashboard');
          } else if (resp.data.role === 'admin') {
            this.props.history.push('/admin/dashboard');
          }
        }, 2000);
      } else {
        let errmsg = "";
        if (resp.data !== null) {
          resp.data.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;
        alert(errmsg, 'error')
        this.setState({ isSubmitting: false })
      }
    } catch (error) {
      alert('Network Error', 'error')
      this.setState({ isSubmitting: false })
    }

  }

  componentDidMount() {
    // alert('Holla');
  }

  render() {
    return (
      <>

        {/* Header */}
        <Header></Header>

        {/* Login */}
        <section className="container my-5 d-flex justify-content-center">

          <div className='col-lg-5 col-12'>

            <div className='border rounded px-3 pt-3 pb-5'>
              <div className='text-center m-0'>
                <span className="fs-1">
                  <img src={logo} width={50} alt="" />
                </span>
              </div>

              <div className='text-center mb-3'>
                <div className='fw-light fs-1 d-lg-none'>Welcome Back</div>
                <div className='fw-light d-none d-lg-inline-block' style={{ fontSize: '3em' }}>Welcome Back</div>
                <div>Don't have an account? <Link className='text-primary' to='/register'>Create an account</Link></div>
              </div>
              <form id='loginForm' onSubmit={(e) => this.submitLogin(e)} noValidate={true} >

                <div className="form-floating mb-4">
                  <input type="email" disabled={this.state.isSubmitting} className="form-control" id="loginEmail" placeholder="name@example.com" />
                  <label htmlFor="loginEmail">Email address</label>
                </div>

                <div className="form-floating mb-1 clearfix">
                  <input type={this.state.showPassword === true ? "text" : "password"} autoComplete='false' disabled={this.state.isSubmitting} className="form-control" id="loginPassword" placeholder='******' />
                  <label htmlFor="loginPassword">Password</label>
                  <div className="fw-bold btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                    this.setState({ showPassword: !this.state.showPassword });
                  }
                  }>{this.state.showPassword === true ? 'Hide Password' : 'Show Password'}</div>
                </div>

                <button className='btn btn-lg w-100 btn-primary px-5' disabled={this.state.isSubmitting} type="submit">
                  {
                    this.state.isSubmitting === true
                      ? <div>
                        <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                        <span className='ms-2 align-middle'>Logging you in...</span>
                      </div>
                      : <span className='ms-2 align-middle'>Login</span>}
                </button>

                <div className="my-4 clearfix">
                  <Link className='text-primary float-end' to='/forgot'>Forgot password</Link>
                </div>

              </form>
            </div>

          </div>

        </section>


        {/* Footer */}
        < Footer ></Footer>

      </>
    );
  }
}

export default Login;