import React, { Component } from 'react';
// images
import Footer from '../../footer/Footer';
import { query, request, alert } from '../../common/Utils';
import logo from '../../assets/imgs/badge.png';
import { AppContext } from '../../context/AppContext';
import Toolbar from '../toolbar/Toolbar';


class Verify extends Component {
  static contextType = AppContext;

  state = {
    isSubmitting: false,
    showPassword: false,
    showConfirmPassword: false,
    slots: [],
    queryMap: {}
  }

  verifyToken = async (e) => {
    e.preventDefault();

    this.setState({ isSubmitting: true })
    let token = query('#verificationToken')[0].value;

    let body = new FormData();
    body.append('token', token);

    let headers = new Headers();
    headers.append('AuthToken', this.context.auth.token);

    try {
      let resp = await request('/api/verifytoken', 'POST', body, headers);

      if (resp.status === true) {
        alert(`${this.context.auth.email} is verified successfully`, 'success');
        this.context.auth.user.verification_status = resp.data.verification_status;

        if (this.context.auth.role === 'member') {
          this.props.history.push('/member/dashboard');
        } else if (this.context.auth.role === 'admin') {
          this.props.history.push('/admin/dashboard');
        }
      } else {
        let errmsg = "";
        if (resp.data !== null) {
          resp.data.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;
        alert(errmsg, 'error')
        this.setState({ isSubmitting: false })
      }
    } catch (error) {
      alert('Network Error', 'error')
      this.setState({ isSubmitting: false })
    }

  }

  sendToken = async () => {
    let headers = new Headers();
    headers.append('AuthToken', this.context.auth.token);

    try {
      let resp = await request('/api/sendtoken', 'POST', null, headers);

      if (resp.status === false) {
        alert(`Verfication token was not sent to ${this.context.auth.email}`, 'error');
      }
    } catch {
      alert('Network Error', 'error')
    }
  }

  async componentDidMount() {
    console.log('opened verify page');
    await this.context.auth.AuthGuard(['member', 'admin']);
    if (this.context.auth.user.verification_status === "verified") {
      if (this.context.auth.role === 'member') {
        this.props.history.push('/member/dashboard');
      } else if (this.context.auth.role === 'admin') {
        this.props.history.push('/admin/dashboard');
      }
    } else {
      this.sendToken();
    }
  }

  render() {
    return (
      <>

        {/* Header */}
        <Toolbar props={this.props}></Toolbar>

        {/* Verify */}
        <section className="container my-5 d-flex justify-content-center">


          <div className='col-lg-5 col-12'>

            <div className='border rounded px-3 py-4'>
              <div className='text-center m-0'>
                <span className="fs-1">
                  <img src={logo} width={50} alt="" />
                </span>
              </div>

              <div className='text-center mb-3'>
                <div className='fw-light fs-1'>Account Verification</div>
                <div>Enter the 5 digit token sent to your email address ({this.context.auth.email})</div>
              </div>
              <form id='verificationForm' onSubmit={(e) => this.verifyToken(e)} noValidate={true} >

                <div className="form-floating mb-4">
                  <input type="text" disabled={this.state.isSubmitting} className="form-control" id="verificationToken" placeholder="******" pattern="[0-9]{6}" required />
                  <label htmlFor="verificationToken">Verification Token</label>
                </div>

                <button className='btn btn-lg w-100 btn-primary px-5' disabled={this.state.isSubmitting} type="submit">
                  {
                    this.state.isSubmitting === true
                      ? <div>
                        <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                        <span className='ms-2 align-middle'>Verifying your account...</span>
                      </div>
                      : <span className='ms-2 align-middle'>Verify</span>}
                </button>

              </form>
            </div>

          </div>

        </section>


        {/* Footer */}
        < Footer ></Footer>

      </>
    );
  }
}

export default Verify;