import React, { Component } from 'react';


// images
import Header from '../header/Header';
import Footer from '../footer/Footer';
import { alert, query, request } from '../common/Utils';
import logo from '../assets/imgs/badge.png';

class ForgotPassword extends Component {
  state = {
    isSubmitting: false,
    email: "",
    canVerify: false,
    canReset: false
  }

  submitForgotPassword = async (e) => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({ isSubmitting: true });
    let forgotForm = query('#forgotForm')[0];

    if (!forgotForm.checkValidity()) {
      forgotForm.classList.add('was-validated')
    } else {
      try {
        let email = query('#forgotEmail')[0].value;
        let body = new FormData();
        body.append('email', email);
        this.setState({ email: email });
        let resp = await request('/api/forgotpassword', 'POST', body);

        if (resp.status === true) {
          alert(resp.message, 'error');
          this.setState({ canVerify: true });
        } else {
          alert(`Verfication token was not sent to ${email}`, 'error');
        }
      } catch {
        alert('Network Error', 'error')
      }
    }
    this.setState({ isSubmitting: false })
  }

  verifyToken = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    this.setState({ isSubmitting: true })
    let token = query('#verificationToken')[0].value;

    let body = new FormData();
    body.append('token', token);
    body.append('email', this.state.email);
    try {
      let resp = await request('/api/forgotpassword/verify', 'POST', body);

      if (resp.status === true) {
        // ResetPassword.state.email = this.state.email;
        alert(`${this.state.email} is verified successfully`, 'success');
        // this.props.history.push('/reset');
        query('#forgotEmail')[0].value = "";
        query('#verificationToken')[0].value = "";
        this.setState({ canReset: true })
      } else {
        alert(resp.message, 'error');
      }
    } catch (error) {
      alert('Network Error', 'error')
    }
    this.setState({ isSubmitting: false })
  }

  submitResetPassword = async (e) => {
    e.preventDefault();
    let resetForm = query('#resetForm')[0];

    if (!resetForm.checkValidity()) {
      e.stopPropagation()
      resetForm.classList.add('was-validated')
    } else {
      this.setState({ isSubmitting: true })

      let password = query('#resetPassword')[0].value;
      let cpassword = query('#resetConfirmPassword')[0].value;

      let body = new FormData();
      body.append('email', this.state.email);
      body.append('password', password);
      body.append('cpassword', cpassword);

      let resp = await request('/api/forgotpassword/reset', 'POST', body);

      if (resp.status === true) {
        alert(`Password reset successful`, 'success');
        setTimeout(() => {
          this.props.history.push('/login');
        }, 2000);
      } else {

        let errmsg = "";
        if (resp.data !== null) {
          resp.data.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;
        alert(errmsg, 'error')
        
      }
    }
    
    this.setState({ isSubmitting: false })
  }

  render() {
    return (
      <>

        {/* Header */}
        <Header></Header>
        {/* <div className='clearfix'></div> */}

        {/* ForgotPassword */}
        <section className="container my-5 d-flex justify-content-center">


          <div className='col-lg-5 col-12'>

            <div className='border rounded my-5 px-3 pt-3 pb-5'>
              <div className='text-center m-0'>
                <div className="fs-1">
                  <img src={logo} width={50} alt="" />
                </div>
              </div>
              {this.state.canReset === false
                ?
                <div>
                  <div className='text-center mb-3'>
                    <div className='fw-light fs-1'>Forgot Password</div>
                    {this.state.canVerify === false ?
                      <span>Enter your email address</span>
                      :
                      <div>Enter the 5 digit token sent to your email address ({this.state.email})</div>
                    }
                  </div>

                  <form id='forgotForm' onSubmit={(e) => this.state.canVerify === false ? this.submitForgotPassword(e) : this.verifyToken(e)} noValidate={true} >

                    <div className="form-floating mb-4">
                      <input type="email" readOnly={this.state.canVerify} disabled={this.state.isSubmitting} className="form-control" id="forgotEmail" placeholder="name@example.com" required />
                      <label htmlFor="forgotEmail">Email address</label>
                      <div className="invalid-feedback">Please enter an email address.</div>
                    </div>

                    {
                      this.state.canVerify === true &&
                      <div className="form-floating mb-4">
                        <input type="text" disabled={this.state.isSubmitting} className="form-control" id="verificationToken" placeholder="******" pattern="[0-9]{5}" />
                        <label htmlFor="verificationToken">Verification Token</label>
                      </div>
                    }

                    <div className='pb-4'>

                      {
                        this.state.canVerify === false ?
                          <button className='btn btn-lg w-100 btn-primary px-5' disabled={this.state.isSubmitting} type="submit">
                            {
                              this.state.isSubmitting === true
                                ? <div>
                                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                                  <span className='ms-2 align-middle'>Submitting...</span>
                                </div>
                                : <span className='ms-2 align-middle'>Submit</span>}
                          </button>
                          :
                          <button className='btn btn-lg w-100 btn-primary px-5' disabled={this.state.isSubmitting} type="submit">
                            {
                              this.state.isSubmitting === true
                                ? <div>
                                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                                  <span className='ms-2 align-middle'>Verifying your account...</span>
                                </div>
                                : <span className='ms-2 align-middle'>Verify</span>}
                          </button>
                      }
                    </div>

                  </form>
                </div>
                :
                <div>
                  <div className='text-center mb-3'>
                    <div className='fw-light fs-1'>Reset Password</div>
                    <span>Choose a new password</span>
                  </div>

                  <form id='resetForm' onSubmit={(e) => this.submitResetPassword(e)} noValidate={true} >
                    <div className="form-floating mb-1 clearfix">
                      <input type={this.state.showPassword === true ? "text" : "password"} disabled={this.state.isSubmitting} className="form-control" id="resetPassword" placeholder='******' required defaultValue="" />
                      <label htmlFor="resetPassword">Password<span className='text-danger ms-1'>*</span></label>
                      <div className="fw-bold btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                        this.setState({ showPassword: !this.state.showPassword });
                      }
                      }>{this.state.showPassword === true ? 'Hide Password' : 'Show Password'}</div>
                      <div className="invalid-feedback">Please enter a secured password.</div>
                    </div>

                    <div className="form-floating mb-1 clearfix">
                      <input type={this.state.showConfirmPassword === true ? "text" : "password"} disabled={this.state.isSubmitting} className="form-control" id="resetConfirmPassword" placeholder='******' required defaultValue="" />
                      <label htmlFor="resetConfirmPassword">Confirm Password<span className='text-danger ms-1'>*</span></label>
                      <div className="fw-bold btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                        this.setState({ showConfirmPassword: !this.state.showConfirmPassword });
                      }
                      }>{this.state.showConfirmPassword === true ? 'Hide Password' : 'Show Password'}</div>
                      <div className="invalid-feedback">Please enter a secured password.</div>
                    </div>

                    <div className='pt-4'>

                      <button className='btn btn-lg w-100 btn-primary px-5' disabled={this.state.isSubmitting} type="submit">
                        {
                          this.state.isSubmitting === true
                            ? <div>
                              <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                              <span className='ms-2 align-middle'>Resetting your password...</span>
                            </div>
                            : <span className='ms-2 align-middle'>Reset</span>}
                      </button>

                    </div>

                  </form>

                </div>
              }
            </div>

          </div>

        </section>


        {/* Footer */}
        < Footer ></Footer>

      </>
    );
  }
}

export default ForgotPassword;