import { faArrowLeft } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { baseUrl, parseUrlQuery, request } from '../../common/Utils';
import defaultProfilePicture from '../../assets/imgs/default_profile_picture.png';
import { AppContext } from '../../context/AppContext';
import Layout from '../../members/layout/Layout';


class AdminUserProfile extends Component {
  static contextType = AppContext;
  state = {
    profile: {}
  }

  getProfile = async (email) => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let body = new FormData();
      body.append('email', email)
      let resp = await request('/api/admin/users/profile', 'POST', body, headers);

      if (resp.status === true) {
        this.setState({ profile: resp.data })
      }
    } catch (error) {
      console.log(error);
    }
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['admin']);
    let query = parseUrlQuery();
    await this.getProfile(query.u ?? '');
  }

  render() {
    return (
      <>
        <Layout props={this.props} page=''>

          <section className='container my-3 px-3 text-secondary'>

            <div className='d-flex justify-content-between align-items-center text-dark'>
              <h1>
                User Profile
                <small className='fs-6 d-block fw-light'>{this.state.profile.role} <b></b> {this.state.profile.verification_status}</small>
              </h1>
              <div className='my-1'>
                <img id='profilePicturePreview' className='rounded-circle' width='60px' height='60px' src={this.state.profile.profile_picture != null ? `${baseUrl}/${this.state.profile.profile_picture}` : defaultProfilePicture} alt="" />
              </div>
            </div>
            <hr className='mt-0' />
            <button className='btn btn-sm btn-muted' onClick={() => { this.props.history.goBack() }}>
              <FontAwesomeIcon icon={faArrowLeft}></FontAwesomeIcon><span className='ms-1'>Back</span>
            </button>

            <div className='mb-3 rounded bor der b order-light shadow-sm px-lg-4 px-3 py-3'>

              <div className='mb-3'>
                <h3>Bio Info</h3>

                <div className='row'>
                  <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                    <h5 className='mt-4'>Personal Info</h5>
                    <div className='mb-1'><small><b>MEMBER ID:</b></small> {this.state.profile.member_id}</div>
                    <div className='mb-1'><small><b>FIRSTNAME:</b></small> {this.state.profile.firstname}</div>
                    <div className='mb-1'><small><b>LASTNAME:</b></small> {this.state.profile.lastname}</div>
                    <div className='mb-1'><small><b>MIDDLENAME:</b></small> {this.state.profile.middlename}</div>
                    <div className='mb-1'><small><b>EMAIL:</b></small> {this.state.profile.email}</div>
                    <div className='mb-1'><small><b>TELEPHONE:</b></small> {this.state.profile.telephone}</div>
                    <div className='mb-1'><small><b>OCCUPATION:</b></small> {this.state.profile.occupation}</div>
                    <div className='mb-1'><small><b>NATIONALITY:</b></small> {this.state.profile.nationality}</div>
                    <div className='mb-1'><small><b>RESIDENTIAL ADDRESS:</b></small> {this.state.profile.residential_address}</div>
                  </div>
                  <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                    <h5 className='mt-4'>Member Info</h5>
                    <div className='mb-1'><small><b>REFERRAL CODE:</b></small> {this.state.profile.referral_code}</div>
                    <div className='mb-1'><small><b>SLOT PACKAGE:</b></small> {this.state.profile.slot_program} ({this.state.profile.no_slots} slots)</div>
                    <div className='mb-1'><small><b>RANK:</b></small> {this.state.profile.rank}</div>

                    <h5 className='mt-4'>Next of Kin Info</h5>
                    <div className='mb-1'><small><b>NEXT OF KIN FULLNAME:</b></small> {this.state.profile.nextofkin_name}</div>
                    <div className='mb-1'><small><b>NEXT OF KIN TELEPHONE:</b></small> {this.state.profile.nextofkin_telephone}</div>
                    <div className='mb-1'><small><b>NEXT OF KIN RESIDENTIAL ADDRESS:</b></small> {this.state.profile.nextofkin_residential_address}</div>
                  </div>
                </div>

              </div>

              <hr />

              <div className='mb-3'>
                <h3>FAVOURITE SPORT &amp; TEAMS</h3>
                <h5 className='mt-4'>FAVOURITE SPORT: {this.state.profile.favourite_sport}</h5>

                <div className='row'>
                  <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                    <div className='mb-1'><small><b>LOCAL TEAM:</b></small> {this.state.profile.favourite_team_local}</div>
                    <div className='mb-1'><small><b>FOREIGN TEAM:</b></small> {this.state.profile.favourite_team_foreign}</div>
                    <div className='mb-1'><small><b>INTERNATIONAL TEAM:</b></small> {this.state.profile.favourite_team_international}</div>
                  </div>
                  <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                    <div className='mb-1'><small><b>CONTINENTAL TEAM:</b></small> {this.state.profile.favourite_team_continental}</div>
                    <div className='mb-1'><small><b>WORLD CUP TEAM:</b></small> {this.state.profile.favourite_team_worldcup}</div>
                    <div className='mb-1'><small><b>OLYMPIC TEAM:</b></small> {this.state.profile.favourite_team_olympic}</div>
                  </div>
                </div>

              </div>

              <hr />

              <div className='mb-3'>
                <h3>BANK DETAILS</h3>

                <div className='row'>
                  <div className='col-lg-6 d-fl ex flex-column align-items-center'>
                    <div className='mb-1'><small><b>BANK:</b></small> {this.state.profile.bankname}</div>
                    <div className='mb-1'><small><b>ACCOUNT NAME:</b></small> {this.state.profile.accountname}</div>
                    <div className='mb-1'><small><b>ACCOUNT NUMBER:</b></small> {this.state.profile.accountnumber}</div>
                  </div>
                </div>

              </div>

            </div>

          </section>

        </Layout>
      </>
    );
  }
}

export default AdminUserProfile;