import { faEdit } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { alert, baseUrl, query, request } from '../../common/Utils';
import defaultProfilePicture from '../../assets/imgs/default_profile_picture.png';
import { AppContext } from '../../context/AppContext';
import Layout from '../layout/Layout';


class MemberProfile extends Component {
  static contextType = AppContext;
  state = {
    isEditingBio: false,
    isEditingBankdetails: false,
    isEditingProfilePicture: false,
    isEditingPassword: false,
    profilePictureDisabled: false,
    bioDisabled: true,
    bankdetailDisabled: true,
    passwordDisabled: true,
    favouriteCompetitionTeams: [],
    mypackage: {},
    countries: [],
    competitions: [],
    selectedNationality: 'Nigeria',

    localCompetition: '',
    localTeams: [],
    foreignCompetition: '',
    foreignTeams: [],
    internationalCompetition: '',
    internationalTeams: [],
    continentalCompetition: '',
    continentalTeams: [],
    worldcupCompetition: '',
    worldcupTeams: [],
    olympicCompetition: '',
    olympicTeams: [],
  }

  preview = (input, dest) => {
    // console.log(input)
    if (input.target.files && input.target.files[0]) {
      let reader = new FileReader();
      reader.onload = function (e) {
        query(`#${dest}`)[0].setAttribute('src', e.target.result);
      }
      reader.readAsDataURL(input.target.files[0])
    }
  }

  updateProfilePicture = async () => {
    alert('Updating profile picture...');
    this.setState({ isEditingProfilePicture: false, profilePictureDisabled: true });

    let body = new FormData();
    let profilepic = query('#profilePicture')[0].files[0];
    body.append('profile-picture', profilepic);

    let headers = new Headers();
    headers.append('AuthToken', this.context.auth.token);

    try {
      let resp = await request('/api/profile/update-profile-picture', 'POST', body, headers);
      if (resp.status === true) {
        this.context.auth.user.profile_picture = resp.data.path;
        this.context.auth.setUser(this.context.auth.user);
        this.setState({ profilePictureDisabled: false });
      }
    } catch (error) {
      alert('Network Error', 'error')
    }

  }

  updatePassword = async (e) => {
    e.preventDefault();
    let updatePasswordForm = query('#updatePasswordForm')[0];

    if (!updatePasswordForm.checkValidity()) {
      e.stopPropagation()
      alert('Please fill in all required inputs correctly', 'error');
      updatePasswordForm.classList.add('was-validated')
    } else {

      // this.setState({ isUpdatingPassword: true })
      this.setState({ passwordDisabled: true });

      let opassword = query('#profileOldPassword')[0];
      let npassword = query('#profileNewPassword')[0];
      let cpassword = query('#profileConfirmNewPassword')[0];
      let body = new FormData();
      body.append('opassword', opassword.value)
      body.append('npassword', npassword.value)
      body.append('cpassword', cpassword.value)

      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);

      try {
        let resp = await request('/api/profile/update-password', 'POST', body, headers);
        if (resp.status === true) {
          alert('Password updated successfully', '', 'success');
          opassword.value = ''; npassword.value = ''; cpassword.value = '';
          this.setState({ isEditingPassword: false })
        } else {
          let errmsg = "";
          if (resp.data !== null) {
            resp.data.map(error => (
              // split the error message into a list
              errmsg += `<li>${error}</li>`
            ))
          } else errmsg = resp.message;
          alert(errmsg, 'error');
          this.setState({ passwordDisabled: false })
        }
      } catch (error) {
        alert('Network Error', 'error')
        this.setState({ passwordDisabled: false })
      }
    }
  }

  updateBankDetails = async (e) => {
    e.preventDefault();
    let updateBankDetailForm = query('#updateBankDetailForm')[0];

    if (!updateBankDetailForm.checkValidity()) {
      e.stopPropagation()
      alert('Please fill in all required inputs correctly', 'error');
      updateBankDetailForm.classList.add('was-validated')
    } else {

      // this.setState({ isUpdatingPassword: true })
      this.setState({ bankdetailDisabled: true });

      let accountnumber = query('#profileAccountnumber')[0];
      let accountname = query('#profileAccountname')[0];
      let bankname = query('#profileBankname')[0];
      let password = query('#profilePassword')[0];

      let body = new FormData();
      body.append('accountnumber', accountnumber.value)
      body.append('accountname', accountname.value)
      body.append('bankname', bankname.value)
      body.append('password', password.value)

      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);

      try {
        let resp = await request('/api/profile/update-bank-details', 'POST', body, headers);
        if (resp.status === true) {
          alert('Bank details updated successfully', 'success');
          this.context.auth.user.accountname = accountname.value;
          this.context.auth.user.accountnumber = accountnumber.value;
          this.context.auth.user.bankname = bankname.value;
          this.context.auth.setUser(this.context.auth.user);
          password.value = '';
          this.setState({ isEditingBankdetails: false })
        } else {
          let errmsg = "";
          if (resp.data !== null) {
            resp.data.map(error => (
              // split the error message into a list
              errmsg += `<li>${error}</li>`
            ))
          } else errmsg = resp.message;
          alert(errmsg, 'error');
          this.setState({ bankdetailDisabled: false })
        }
      } catch (error) {
        alert('Network Error', 'error', false)
        this.setState({ bankdetailDisabled: false })
      }
    }
  }

  updateProfileBio = async (e) => {
    e.preventDefault();
    let updateBioForm = query('#updateBioForm')[0];

    if (!updateBioForm.checkValidity()) {
      e.stopPropagation()
      alert('Please fill in all required inputs correctly', 'error');
      updateBioForm.classList.add('was-validated')
    } else {

      // this.setState({ isUpdatingPassword: true })
      this.setState({ bioDisabled: true });

      let firstname = query('#profileFirstname')[0]
      let lastname = query('#profileLastname')[0]
      let middlename = query('#profileMiddlename')[0]
      let telephone = query('#profileTelephone')[0]
      let nationality = query('#profileNationality')[0]
      let occupation = query('#profileOccupation')[0]
      let residential_address = query('#profileResidentialAddress')[0]
      let nextofkin_name = query('#profileNOKName')[0]
      let nextofkin_telephone = query('#profileNOKTelephone')[0]
      let nextofkin_residential_address = query('#profileNOKResidentialAddress')[0]
      let favourite_sport = query('#profileFavSport')[0]

      let favourite_team_local, favourite_team_foreign, favourite_team_international, favourite_team_continental, favourite_team_worldcup, favourite_team_olympic = null;
      favourite_team_local = query('#profileFavTeamLocal')[0];
      if (this.state.mypackage.no_slots > 1) { favourite_team_foreign = query('#profileFavTeamForeign')[0]; }
      if (this.state.mypackage.no_slots > 2) { favourite_team_international = query('#profileFavTeamInternational')[0]; }
      if (this.state.mypackage.no_slots > 3) { favourite_team_continental = query('#profileFavTeamContinental')[0]; }
      if (this.state.mypackage.no_slots > 4) { favourite_team_worldcup = query('#profileFavTeamWorldcup')[0]; }
      if (this.state.mypackage.no_slots > 5) { favourite_team_olympic = query('#profileFavTeamOlympic')[0]; }

      let body = new FormData();
      body.append('firstname', firstname.value);
      body.append('lastname', lastname.value);
      body.append('middlename', middlename.value);
      body.append('telephone', telephone.value);
      body.append('nationality', nationality.value);
      body.append('occupation', occupation.value);
      body.append('residential_address', residential_address.value);
      body.append('nextofkin_name', nextofkin_name.value);
      body.append('nextofkin_telephone', nextofkin_telephone.value);
      body.append('nextofkin_residential_address', nextofkin_residential_address.value);
      body.append('favourite_sport', favourite_sport.value);

      body.append('favourite_team_local', favourite_team_local.value ?? '');
      if (this.state.mypackage.no_slots > 1) { body.append('favourite_team_foreign', favourite_team_foreign.value ?? ''); }
      if (this.state.mypackage.no_slots > 2) { body.append('favourite_team_international', favourite_team_international.value ?? ''); }
      if (this.state.mypackage.no_slots > 3) { body.append('favourite_team_continental', favourite_team_continental.value ?? ''); }
      if (this.state.mypackage.no_slots > 4) { body.append('favourite_team_worldcup', favourite_team_worldcup.value ?? ''); }
      if (this.state.mypackage.no_slots > 5) { body.append('favourite_team_olympic', favourite_team_olympic.value ?? ''); }

      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);

      try {
        let resp = await request('/api/profile/update-bio', 'POST', body, headers);
        if (resp.status === true) {
          alert('Profile updated successfully', 'success');
          this.context.auth.user.firstname = firstname.value;
          this.context.auth.user.lastname = lastname.value;
          this.context.auth.user.middlename = middlename.value;
          this.context.auth.user.telephone = telephone.value;
          this.context.auth.user.nationality = nationality.value;
          this.context.auth.user.occupation = occupation.value;
          this.context.auth.user.residential_address = residential_address.value;
          this.context.auth.user.nextofkin_name = nextofkin_name.value;
          this.context.auth.user.nextofkin_telephone = nextofkin_telephone.value;
          this.context.auth.user.nextofkin_residential_address = nextofkin_residential_address.value;
          this.context.auth.user.favourite_sport = favourite_sport.value;

          this.context.auth.user.favourite_team_local = favourite_team_local.value;
          if (this.state.mypackage.no_slots > 1) { this.context.auth.user.favourite_team_foreign = favourite_team_foreign.value; }
          if (this.state.mypackage.no_slots > 2) { this.context.auth.user.favourite_team_international = favourite_team_international.value; }
          if (this.state.mypackage.no_slots > 3) { this.context.auth.user.favourite_team_continental = favourite_team_continental.value; }
          if (this.state.mypackage.no_slots > 4) { this.context.auth.user.favourite_team_worldcup = favourite_team_worldcup.value; }
          if (this.state.mypackage.no_slots > 5) { this.context.auth.user.favourite_team_olympic = favourite_team_olympic.value; }

          this.context.auth.setUser(this.context.auth.user);
          this.setState({ isEditingBio: false })
        } else {
          let errmsg = "";
          if (resp.data !== null) {
            resp.data.map(error => (
              // split the error message into a list
              errmsg += `<li>${error}</li>`
            ))
          } else errmsg = resp.message;
          alert(errmsg, 'error');
          this.setState({ bioDisabled: false })
        }
      } catch (error) {
        console.log(error);
        alert('Network Error', 'error', false)
        this.setState({ bioDisabled: false })
      }
    }
  }

  getPackage = async () => {
    // get my slot program
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/mypackage', 'GET', null, headers);

      if (resp.status === true) {
        this.setState({ mypackage: resp.data })
      }
    } catch (error) {
      console.log(error);
    }
  }

  getCountriesAndCompetitions = async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);

      // countries
      let resp = await request('/api/getcountries', 'GET', null, headers);
      if (resp.status === true) {
        this.setState({ countries: resp.data })
        // set default nationality of user
        query('#profileNationality')[0].value = this.context.auth.user.nationality ?? 'Nigeria';
      }

      // competitions
      resp = await request('/api/getcompetitions', 'GET', null, headers);
      if (resp.status === true) {
        this.setState({ competitions: resp.data })
        this.setCompeitionsAndQualifiedTeams();
      }

    } catch (error) {
      console.log(error);
    }
  }

  setNationality = async (e) => {
    let selectedNation = this.state.countries.find((country) => { return country.country === e.target.value; });
    await this.setState({
      selectedNationality: selectedNation.country
    });
    this.setCompeitionsAndQualifiedTeams();
  }

  setCompeitionsAndQualifiedTeams = () => {
    console.log('setting teams...')
    let sport = query('#profileFavSport')[0].value ?? 'Football';
    let nation = this.state.selectedNationality;
    let continent = this.state.countries.find((country) => { return country.country === nation; }).continent;

    let local = this.state.competitions
      .find((competition) => { return competition.sport === sport && competition.region === nation; })

    if (local === undefined) {
      alert(`No ${sport} Competition for ${nation}`, 'error');
      this.setState({
        localCompetition: "No competition",
        localTeams: []
      });
    } else {
      this.setState({
        localCompetition: local.competition,
        localTeams: JSON.parse(local.qualified_teams)
      });
    };

    let foreign = this.state.competitions
      .find((competition) => { return competition.sport === sport && competition.region === continent && competition.slotpackage === '2'; })

    if (foreign === undefined) {
      alert(`No ${sport} Competition for ${continent}`, 'error');
      this.setState({
        foreignCompetition: "No competition",
        foreignTeams: []
      });
    } else {
      this.setState({
        foreignCompetition: foreign.competition,
        foreignTeams: JSON.parse(foreign.qualified_teams)
      });
    };

    let international = this.state.competitions
      .filter((competition) => { return competition.sport === sport && competition.region !== continent && competition.slotpackage === '2'; })

    console.log(international); console.log(foreign);
    if (international === undefined) {
      alert(`No ${sport} Competition for ${continent}`, 'error');
      this.setState({
        internationalCompetition: "No competition",
        internationalTeams: []
      });
    } else {
      this.setState({
        internationalCompetition: international[0].competition,
        internationalTeams: international,
      });
      this.renderInternationalCompetitionAndTeams()
    };

    let continental = this.state.competitions
      .find((competition) => { return competition.sport === sport && competition.region === continent && competition.slotpackage === '4'; });

    console.log(continental);
    if (continental === undefined) {
      alert(`No ${sport} Competition for ${continent}`, 'error');
      this.setState({
        continentalCompetition: "No competition",
        continentalTeams: []
      });
    } else {
      this.setState({
        continentalCompetition: continental.competition,
        continentalTeams: JSON.parse(continental.qualified_teams)
      });
    };

    let worldcup = this.state.competitions
      .find((competition) => { return competition.sport === sport && competition.region === 'World Wide' && competition.slotpackage === '5'; })

    if (worldcup === undefined) {
      alert(`No ${sport} Competition for ${continent}`, 'error');
      this.setState({
        worldcupCompetition: "No competition",
        worldcupTeams: []
      });
    } else {
      this.setState({
        worldcupCompetition: worldcup.competition,
        worldcupTeams: JSON.parse(worldcup.qualified_teams)
      });
    };

    let olympic = this.state.competitions
      .find((competition) => { return competition.sport === sport && competition.region === 'World Wide' && competition.slotpackage === '6'; });

    if (olympic === undefined) {
      alert(`No ${sport} Competition for ${continent}`, 'error');
      this.setState({
        olympicCompetition: "No competition",
        olympicTeams: []
      });
    } else {
      this.setState({
        olympicCompetition: olympic.competition,
        olympicTeams: JSON.parse(olympic.qualified_teams)
      });
    };


  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['member', 'admin']);
    await this.getPackage();
    await this.getCountriesAndCompetitions();
  }

  setInternationalCompetition = (competition) => {
    console.log(competition);
    competition = competition.substr(competition.indexOf('-') + 2);
    this.setState({ internationalCompetition: competition });
  }

  renderInternationalCompetitionAndTeams = () => {

    query('#profileFavTeamInternational')[0].innerHTML = "";
    this.state.internationalTeams.forEach((continent) => {
      let competition = continent.competition;
      let teams = JSON.parse(continent.qualified_teams);

      // console.log(competition);
      let teamsString = ``;
      teams.forEach((team, key) => (
        teamsString += `<option key=${key} value='${team} - ${competition}'>${team}</option>`
      ));

      query('#profileFavTeamInternational')[0].insertAdjacentHTML('beforeEnd',
        `<optgroup label="${competition}">
          ${teamsString}
        </optgroup>`
      );

    })
  }

  render() {
    return (
      <>
        <Layout props={this.props} page='/profile'>

          <section className='container my-3 px-3 text-secondary'>

            <div className='d-flex justify-content-between text-dark'>
              <h1>
                Profile
              <small className='fs-6 d-block fw-light'>{this.context.auth.user.email}</small>
              </h1>
              <div className='d-flex flex-column justify-content-center position-relative'>
                <label style={{ cursor: 'pointer' }} htmlFor='profilePicture'>
                  <img id='profilePicturePreview' className='rounded-circle' width='60px' height='60px' src={this.context.auth.user.profile_picture != null ? `${baseUrl}/${this.context.auth.user.profile_picture}` : defaultProfilePicture} alt="" />
                </label>
                <input disabled={this.state.profilePictureDisabled} type="file" onChange={(e) => { this.preview(e, 'profilePicturePreview'); this.setState({ isEditingProfilePicture: true }) }} className='d-none' id='profilePicture' />
                {this.state.isEditingProfilePicture && <div htmlFor='profilePicture' onClick={() => this.updateProfilePicture()} className='position-absolute w-100 bottom-0 btn btn-sm btn-secondary py-0 m-0 rounded '>Update</div>}
              </div>
            </div>
            <hr />

            <div className='my-4 border rounded px-4 py-4'>

              <div className='d-flex justify-content-between'>
                <h3>Bio Info</h3>
                <div>
                  <FontAwesomeIcon onClick={() => this.setState({ bioDisabled: !this.state.bioDisabled, isEditingBio: !this.state.isEditingBio })} className="btn btn-sm m-0 p-0 btn-muted fs-5 text-primary" icon={faEdit} ></FontAwesomeIcon>
                </div>
              </div>

              <form id='updateBioForm' onSubmit={(e) => this.updateProfileBio(e)} noValidate={true} >

                <h5 className='mt-4'>Personal Info</h5>

                <div className='row'>

                  <div className="col-lg-6 form-floating mb-4">
                    <input type="text" disabled={this.state.bioDisabled} className="form-control" id="profileFirstname" placeholder="firstname" required defaultValue={this.context.auth.user.firstname} />
                    <label htmlFor="profileFirstname" className='ps-4'>First Name<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter your first name.</div>
                  </div>

                  <div className="col-lg-6 form-floating mb-4">
                    <input type="text" disabled={this.state.bioDisabled} className="form-control" id="profileLastname" placeholder="lastname" required defaultValue={this.context.auth.user.lastname} />
                    <label htmlFor="profileLastname" className='ps-4'>Last Name<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter your last name.</div>
                  </div>

                </div>

                <div className="form-floating mb-4">
                  <input type="tel" disabled={this.state.bioDisabled} className="form-control" id="profileMiddlename" placeholder="telephone" required defaultValue={this.context.auth.user.middlename} />
                  <label htmlFor="profileMiddlename" className=''>Middle Name<span className='text-danger ms-1'>*</span></label>
                  <div className="invalid-feedback">Please enter your middle name.</div>
                </div>

                <div className='row'>

                  <div className="col-lg-6 form-floating mb-4">
                    <input type="tel" disabled={this.state.bioDisabled} className="form-control" id="profileTelephone" placeholder="telephone" required defaultValue={this.context.auth.user.telephone} />
                    <label htmlFor="profileTelephone" className='ps-4'>Telephone<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter your first name.</div>
                  </div>

                  <div className="col-lg-6 form-floating mb-4">
                    <input type="text" disabled={this.state.bioDisabled} className="form-control" id="profileOccupation" placeholder="occupation" required defaultValue={this.context.auth.user.occupation} />
                    <label htmlFor="profileOccupation" className='ps-4'>Occupation<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter your occupation.</div>
                  </div>

                </div>

                <div className="form-floating mb-4">
                  <select disabled={this.state.bioDisabled} onChange={this.setNationality} className="form-select" id="profileNationality" placeholder="nationality" required defaultValue={this.context.auth.user.nationality ?? 'Nigeria'}>
                    {
                      this.state.countries.map((country, key) => (
                        <option key={key} value={country.country}>{country.country}</option>
                      ))
                    }
                  </select>
                  <label htmlFor="profileNationality" className=''>Nationality<span className='text-danger ms-1'>*</span></label>
                  <div className="invalid-feedback">Please choose your nationality.</div>
                </div>

                <div className="form-floating mb-4">
                  <textarea className="form-control" disabled={this.state.bioDisabled} placeholder="address" id="profileResidentialAddress" style={{ height: '100px' }} defaultValue={this.context.auth.user.residential_address} required />
                  <label htmlFor="profileResidentialAddress">Residential Address<span className='text-danger ms-1'>*</span></label>
                  <div className="invalid-feedback">Please enter your residentail address.</div>
                </div>

                <h5 className='mt-4'>Next of Kin Info</h5>

                <div className='row'>

                  <div className="col-lg-6 form-floating mb-4">
                    <input type="text" disabled={this.state.bioDisabled} className="form-control" id="profileNOKName" placeholder="fullname" required defaultValue={this.context.auth.user.nextofkin_name} />
                    <label htmlFor="profileNOKName" className='ps-4'>Next of Kin Name<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter the full name of your next of kin.</div>
                  </div>

                  <div className="col-lg-6 form-floating mb-4">
                    <input type="tel" disabled={this.state.bioDisabled} className="form-control" id="profileNOKTelephone" placeholder="telephone" required defaultValue={this.context.auth.user.nextofkin_telephone} />
                    <label htmlFor="profileNOKTelephone" className='ps-4'>Next of Kin Telephone<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter the telephone number of your next of kin.</div>
                  </div>

                </div>

                <div className="form-floating mb-4">
                  <textarea className="form-control" disabled={this.state.bioDisabled} placeholder="Leave a comment here" id="profileNOKResidentialAddress" style={{ height: '100px' }} defaultValue={this.context.auth.user.nextofkin_residential_address} required />
                  <label htmlFor="profileNOKResidentialAddress">Next of Kin Residential Address<span className='text-danger ms-1'>*</span></label>
                  <div className="invalid-feedback">Please enter your next of kin residential address.</div>
                </div>

                <h5 className='mt-4'>
                  {/* <img width='20px' className='me-2' src='https://apiv2.apifootball.com/badges/2612_everton.png' alt='team badge' /> */}
                  Your Favourite Team Info
                  <br />
                  <small className='fw-normal' style={{ fontSize: '14px' }}>
                    <span className='text-danger ms-1'>*</span>
                    Note that you cannot change your favourite sport or favourite team after choosing now.
                  </small>
                </h5>

                <div className="form-floating mb-4">
                  {
                    this.context.auth.user.favourite_sport === null ?
                      <select disabled={this.state.bioDisabled} className="form-select" id="profileFavSport" placeholder="sport" required value={this.context.auth.user.favourite_sport}>
                        {
                          [
                            "Football",
                            // "American Football", "Baseball", "Basketball", "Car Racing", "Boxing", "Swimming", "Tennis", "Golf", "Racing", "Hockey"
                          ].map((sport, key) => (
                            <option key={key} value={sport}>{sport}</option>
                          ))
                        }
                      </select>
                      :
                      <input type="text" readOnly={true} className="form-control" id="profileFavSport" defaultValue={this.context.auth.user.favourite_sport} />
                  }
                  <label htmlFor="profileFavSport" className=''>Favourite Sport<span className='text-danger ms-1'>*</span></label>
                  <div className="invalid-feedback">Please enter your favourite sport.</div>
                </div>

                {/* This should be dependent on the slot chosen, sport chosen, and the competition available for the sport in that locality (slot chosen decide the locality) */}

                <div className='row'>

                  <div className="col-lg-6 form-floating mb-4">
                    {
                      this.context.auth.user.favourite_team_local === null ?
                        < select readOnly={true} disabled={this.state.bioDisabled} className="form-select" id="profileFavTeamLocal" placeholder="sport" required defaultValue={this.context.auth.user.favourite_team_local}>
                          {
                            this.state.localTeams.map((team, key) => (
                              <option key={key} value={`${team} - ${this.state.localCompetition}`}>{team}</option>
                            ))
                          }
                        </select>
                        :
                        <input type="text" readOnly={true} className="form-control" id="profileFavTeamLocal" defaultValue={this.context.auth.user.favourite_team_local} />
                    }
                    <label htmlFor="profileFavTeamLocal" className='ps-4'>Favourite Local Team<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter your favourite local team.</div>
                    <span className='text-primary fs-6'>{this.state.localCompetition}</span>
                  </div>


                  {this.state.mypackage.no_slots > 1 &&
                    <div className="col-lg-6 form-floating mb-4">
                      {
                        this.context.auth.user.favourite_team_foreign === null ?
                          <select disabled={this.state.bioDisabled} className="form-select" id="profileFavTeamForeign" placeholder="sport" required defaultValue={this.context.auth.user.favourite_team_foreign}>
                            {
                              this.state.foreignTeams.map((team, key) => (
                                <option key={key} value={`${team} - ${this.state.foreignCompetition}`}>{team}</option>
                              ))
                            }
                          </select>
                          :
                          <input type="text" readOnly={true} className="form-control" id="profileFavTeamForeign" defaultValue={this.context.auth.user.favourite_team_foreign} />
                      }
                      <label htmlFor="profileFavTeamForeign" className='ps-4'>Favourite Foreign Team<span className='text-danger ms-1'>*</span></label>
                      <div className="invalid-feedback">Please enter your favourite foreign team.</div>
                      <span className='text-primary fs-6'>{this.state.foreignCompetition}</span>
                    </div>
                  }
                </div>

                <div className='row'>

                  {this.state.mypackage.no_slots > 2 &&
                    <div className="col-lg-6 form-floating mb-4">
                      {
                        this.context.auth.user.favourite_team_international === null ?
                          <select disabled={this.state.bioDisabled} className="form-select" id="profileFavTeamInternational" placeholder="sport" required defaultValue={this.context.auth.user.favourite_team_international} onChange={(e) => this.setInternationalCompetition(e.target.value)}>
                            {/* {this.renderInternationalCompetitionAndTeams()} */}
                          </select>
                          :
                          <input type="text" readOnly={true} className="form-control" id="profileFavTeamInternational" defaultValue={this.context.auth.user.favourite_team_international} />
                      }
                      <label htmlFor="profileFavTeamInternational" className='ps-4'>Favourite International Team<span className='text-danger ms-1'>*</span></label>
                      <div className="invalid-feedback">Please enter your favourite international team.</div>
                      <span className='text-primary fs-6'>{this.state.internationalCompetition}</span>
                    </div>
                  }

                  {this.state.mypackage.no_slots > 3 &&
                    <div className="col-lg-6 form-floating mb-4">
                      {
                        this.context.auth.user.favourite_team_continental === null ?
                          <select disabled={this.state.bioDisabled} className="form-select" id="profileFavTeamContinental" placeholder="sport" required defaultValue={this.context.auth.user.favourite_team_continental}>
                            {
                              this.state.continentalTeams.map((team, key) => (
                                <option key={key} value={`${team} - ${this.state.continentalCompetition}`}>{team}</option>
                              ))
                            }
                          </select>
                          :
                          <input type="text" readOnly={true} className="form-control" id="profileFavTeamContinental" defaultValue={this.context.auth.user.favourite_team_continental} />
                      }
                      <label htmlFor="profileFavTeamContinental" className='ps-4'>Favourite Continental Team<span className='text-danger ms-1'>*</span></label>
                      <div className="invalid-feedback">Please enter your favourite continental team.</div>
                      <span className='text-primary fs-6'>{this.state.continentalCompetition}</span>
                    </div>
                  }

                </div>

                <div className='row'>

                  {this.state.mypackage.no_slots > 4 &&
                    <div className="col-lg-6 form-floating mb-4">
                      {
                        this.context.auth.user.favourite_team_worldcup === null ?
                          <select disabled={this.state.bioDisabled} className="form-select" id="profileFavTeamWorldcup" placeholder="sport" required defaultValue={this.context.auth.user.favourite_team_worldcup}>
                            {
                              this.state.worldcupTeams.map((team, key) => (
                                <option key={key} value={`${team} - ${this.state.worldcupCompetition}`}>{team}</option>
                              ))
                            }
                          </select>
                          :
                          <input type="text" readOnly={true} className="form-control" id="profileFavTeamWorldcup" defaultValue={this.context.auth.user.favourite_team_worldcup} />
                      }
                      <label htmlFor="profileFavTeamWorldcup" className='ps-4'>Favourite World Cup Team<span className='text-danger ms-1'>*</span></label>
                      <div className="invalid-feedback">Please enter your favourite world cup team.</div>
                      <span className='text-primary fs-6'>{this.state.worldcupCompetition}</span>
                    </div>
                  }

                  {this.state.mypackage.no_slots > 5 &&
                    <div className="col-lg-6 form-floating mb-4">
                      {
                        this.context.auth.user.favourite_team_olympic === null ?
                          <select disabled={this.state.bioDisabled} className="form-select" id="profileFavTeamOlympic" placeholder="sport" required defaultValue={this.context.auth.user.favourite_team_olympic}>
                            {
                              this.state.olympicTeams.map((team, key) => (
                                <option key={key} value={`${team} - ${this.state.olympicCompetition}`}>{team}</option>
                              ))
                            }
                          </select>
                          :
                          <input type="text" readOnly={true} className="form-control" id="profileFavTeamOlympic" defaultValue={this.context.auth.user.favourite_team_olympic} />
                      }
                      <label htmlFor="profileFavTeamOlympic" className='ps-4'>Favourite Olympic Team<span className='text-danger ms-1'>*</span></label>
                      <div className="invalid-feedback">Please enter your favourite olympic team.</div>
                      <span className='text-primary fs-6'>{this.state.olympicCompetition}</span>
                    </div>
                  }

                </div>

                {
                  this.state.isEditingBio &&
                  <button className='mt-lg-3 btn btn-lg w-100 btn-primary px-5' disabled={this.state.bioDisabled} type="submit">
                    {
                      this.state.isSubmittingBio === true
                        ? <div>
                          <span class="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                          <span className='ms-2 align-middle'>Updating your profile...</span>
                        </div>
                        : <span className='ms-2 align-middle'>Update</span>}
                  </button>
                }

              </form>

            </div>

            <div className='my-4 border rounded px-4 py-4'>

              <div className='d-flex justify-content-between'>
                <h3>Bank Details</h3>
                <div>
                  <FontAwesomeIcon className="btn btn-sm m-0 p-0 btn-muted fs-5 text-primary" onClick={() => this.setState({ bankdetailDisabled: !this.state.bankdetailDisabled, isEditingBankdetails: !this.state.isEditingBankdetails })} icon={faEdit} ></FontAwesomeIcon>
                </div>
              </div>

              <form id='updateBankDetailForm' onSubmit={(e) => this.updateBankDetails(e)} noValidate={true} >

                <div className='row'>

                  <div className="col-lg-6 form-floating mb-4">
                    <input type="text" disabled={this.state.bankdetailDisabled} className="form-control" id="profileAccountnumber" placeholder="accountnumber" required defaultValue={this.context.auth.user.accountnumber} />
                    <label htmlFor="profileAccountnumber" className='ps-4'>Account Number<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter your bank account number.</div>
                  </div>

                  <div className="col-lg-6 form-floating mb-4">
                    <input type="text" disabled={this.state.bankdetailDisabled} className="form-control" id="profileAccountname" placeholder="accountname" required defaultValue={this.context.auth.user.accountname} />
                    <label htmlFor="profileAccountname" className='ps-4'>Account Name<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter your bank account name.</div>
                  </div>

                </div>

                <div className="form-floating mb-4">
                  <select disabled={this.state.bankdetailDisabled} className="form-select" id="profileBankname" placeholder="bankname" required defaultValue={this.context.auth.user.bankname ?? ''}>
                    {
                      ["Guaranty Trust Bank", "First Bank", "Polaris Bank", "UBA", "Zenith Bank", "Keystone Bank", "Access Bank", "Fidelity Bank", "FCMB"].map((bank, key) => (
                        <option key={key} value={bank}>{bank}</option>
                      ))
                    }
                  </select>
                  <label htmlFor="profileBankname" className=''>Bank Name<span className='text-danger ms-1'>*</span></label>
                  <div className="invalid-feedback">Please select your bank.</div>
                </div>

                <div className="form-floating mb-1 clearfix">
                  <input type={this.state.showPassword === true ? "text" : "password"} autoComplete='off' disabled={this.state.bankdetailDisabled} className="form-control" id="profilePassword" placeholder='******' required />
                  <label htmlFor="profilePassword">Password<span className='text-danger ms-1'>*</span></label>
                  <div className="invalid-feedback">Please enter your password.</div>
                  <div className="fw-bold btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                    this.setState({ showPassword: !this.state.showPassword });
                  }
                  }>{this.state.showPassword === true ? 'Hide Password' : 'Show Password'}</div>
                </div>

                {
                  this.state.isEditingBankdetails &&
                  <button className='mt-lg-3 btn btn-lg w-100 btn-primary px-5' disabled={this.state.bankdetailDisabled} type="submit">
                    {
                      this.state.isSubmittingBio === true
                        ? <div>
                          <span class="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                          <span className='ms-2 align-middle'>Updating your profile...</span>
                        </div>
                        : <span className='ms-2 align-middle'>Update</span>}
                  </button>
                }

              </form>

            </div>

            <div className='my-4 border border-danger rounded px-4 py-4'>

              <div className='d-flex justify-content-between'>
                <h3>Update Password</h3>
                <div>
                  <FontAwesomeIcon className="btn btn-sm m-0 p-0 btn-muted fs-5 text-primary" onClick={() => this.setState({ passwordDisabled: !this.state.passwordDisabled, isEditingPassword: !this.state.isEditingPassword })} icon={faEdit} ></FontAwesomeIcon>
                </div>
              </div>

              <form id='updatePasswordForm' onSubmit={(e) => this.updatePassword(e)} noValidate={true} >

                <div className="form-floating mb-1 clearfix">
                  <input type={this.state.showOPassword === true ? "text" : "password"} autoComplete='off' disabled={this.state.passwordDisabled} className="form-control" id="profileOldPassword" placeholder='******' required />
                  <label htmlFor="profileOldPassword">Old Password<span className='text-danger ms-1'>*</span></label>
                  <div className="invalid-feedback">Please enter your old password.</div>
                  <div className="fw-bold btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                    this.setState({ showOPassword: !this.state.showOPassword });
                  }
                  }>{this.state.showOPassword === true ? 'Hide Password' : 'Show Password'}</div>
                </div>

                <div className='row'>

                  <div className="col-lg-6 form-floating mb-1 clearfix">
                    <input type={this.state.showNPassword === true ? "text" : "password"} autoComplete='off' disabled={this.state.passwordDisabled} className="form-control" id="profileNewPassword" placeholder='******' required />
                    <label htmlFor="profileNewPassword" className='ps-4'>New Password<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter your new password.</div>
                    <div className="fw-bold btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                      this.setState({ showNPassword: !this.state.showNPassword });
                    }
                    }>{this.state.showNPassword === true ? 'Hide Password' : 'Show Password'}</div>
                  </div>

                  <div className="col-lg-6 form-floating mb-1 clearfix">
                    <input type={this.state.showCPassword === true ? "text" : "password"} autoComplete='off' disabled={this.state.passwordDisabled} className="form-control" id="profileConfirmNewPassword" placeholder='******' required />
                    <label htmlFor="profileConfirmNewPassword" className='ps-4'>Confirm New Password<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please confirm your new password.</div>
                    <div className="fw-bold btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                      this.setState({ showCPassword: !this.state.showCPassword });
                    }
                    }>{this.state.showCPassword === true ? 'Hide Password' : 'Show Password'}</div>
                  </div>

                </div>

                {
                  this.state.isEditingPassword &&
                  <button className='mt-lg-3 btn btn-lg w-100 btn-primary px-5' disabled={this.state.passwordDisabled} type="submit">
                    {
                      this.state.passwordDisabled === true
                        ? <div>
                          <span class="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                          <span className='ms-2 align-middle'>Updating your password...</span>
                        </div>
                        : <span className='ms-2 align-middle'>Update</span>}
                  </button>
                }

              </form>

            </div>


          </section>


        </Layout>
      </>
    );
  }
}

