function snackbar(title, message, swatch = 'alert-info') {
    // get the alert
    var snackbar = query('#snackbar')[0];
    // set the title, subtitle, swatch & message
    var notification = query('#notification')[0];
    notification.style.zIndex = "1070";
    query('#snackbar > div')[0].classList.add(swatch);
    query('#snackbar > div > div > .title')[0].innerHTML = title;
    query('#snackbar > div > .message')[0].innerHTML = message;
    var toast = new Toast(snackbar);
    toast.show();
    snackbar.addEventListener('hidden.bs.toast', function () {
        query('#snackbar > div')[0].classList.remove(swatch);
        notification.style.zIndex = "-1";
    });
}

