import React from 'react';

const AppContext = React.createContext();
const AppProvider = AppContext.Provider;
const AppConsumer = AppContext.Consumer;

class Auth {

  token;
  email;
  role;
  permissions;
  authenticated;
  user = {
    isset: false,
  };

  constructor() {
    this.token = '';
    this.email = '';
    this.role = 'guest';
    this.permissions = [];
    this.authenticated = false;

    // user
    this.user.isset = false;
    this.user.notifications = 0;
    this.user.accountname = "";
    this.user.accountnumber = "";
    this.user.bankname = "";
    this.user.created_at = "";
    this.user.email = "";
    this.user.favourite_sport = "";
    this.user.favourite_team_local = "";
    this.user.favourite_team_foreign = "";
    this.user.favourite_team_international = "";
    this.user.favourite_team_continental = "";
    this.user.favourite_team_worldcup = "";
    this.user.favourite_team_olympic = "";
    this.user.firstname = "";
    this.user.lastname = "";
    this.user.member_id = "";
    this.user.middlename = "";
    this.user.nationality = "";
    this.user.nextofkin_name = "";
    this.user.nextofkin_residential_address = "";
    this.user.nextofkin_telephone = "";
    this.user.occupation = "";
    this.user.profile_picture = null;
    this.user.referral_code = "";
    this.user.residential_address = "";
    this.user.telephone = "";
    this.user.verification_status = "";
    this.user.payment_status = ""
    this.user.slot_program = ""
  }

  async persistAuth() {
    new Promise((resolve, reject) => {
      let auth = JSON.parse(localStorage.getItem('Auth')) ?? {
        token: '',
        email: '',
        role: 'guest',
        permissions: [],
        authenticated: false,
      };
      resolve(auth);
    })
      .then(auth => {
        this.token = auth.token;
        this.email = auth.email;
        this.role = auth.role;
        this.permissions = auth.permissions;
        this.authenticated = auth.authenticated;
      })

    if (this.user.isset === false) {
      new Promise((resolve, reject) => {
        let user = JSON.parse(localStorage.getItem('User')) ?? {
          isset: false,
          notifications: 0,
          accountname: "",
          accountnumber: "",
          bankname: "",
          created_at: "",
          email: "",
          favourite_sport: "",
          favourite_team_local: "",
          favourite_team_foreign: "",
          favourite_team_international: "",
          favourite_team_continental: "",
          favourite_team_worldcup: "",
          favourite_team_olympic: "",
          firstname: "",
          lastname: "",
          member_id: "",
          middlename: "",
          nationality: "",
          nextofkin_name: "",
          nextofkin_residential_address: "",
          nextofkin_telephone: "",
          occupation: "",
          profile_picture: null,
          referral_code: "",
          residential_address: "",
          telephone: "",
          verification_status: "",
          payment_status: "",
          slot_program: ""
        };
        resolve(user);
      })
        .then(user => {
          this.setUser(user);
        })
    }

  }

  async AuthLogin(token, email, role, permissions = []) {
    localStorage.setItem('Auth', JSON.stringify({
      token: token,
      email: email,
      role: role,
      permissions: permissions,
      authenticated: true
    }));
    this.token = token;
    this.email = email;
    this.role = role;
    this.permissions = permissions;
    this.authenticated = true;
  }

  async setUser(user) {
    localStorage.setItem('User', JSON.stringify(user));
    this.user = user;
    this.user.isset = true;
  }

  AuthLogout() {
    localStorage.clear();
    this.token = '';
    this.email = '';
    this.role = 'guest';
    this.permissions = [];
    this.authenticated = false;
    this.user = {}
  }

  async AuthGuard(role, permissions) {
    // await this.persistAuth()
    new Promise((resolve, reject) => {
      resolve(this.persistAuth());
      // reject(console.log('AuthGuard reject'));
      // reject(this.AuthLogout());
    }).then(() => {

      console.log(this.authenticated);

      // check for authentication
      if (this.authenticated === false) {
        this.AuthLogout();
        window.location.href = '/login';
      }

      // check the role
      if (role !== null) {
        // if (this.role !== role) return false;
        if (!role.includes(this.role)) {
          this.AuthLogout();
          window.location.href = '/login';
        }
        console.log('correct role')
      }

      // check permissions
      if (permissions !== undefined) {
        if (!this.permissions.includes(permissions)) {
          this.AuthLogout();
          window.location.href = '/login';
        }
      }

      // else return true
      return true;
    });

  }

}

export { AppContext, AppProvider, AppConsumer, Auth };