import React, { Component } from 'react';

// images
import tennis from "../assets/imgs/bgs/race-track.jpg";
import Header from '../header/Header';
import Footer from '../footer/Footer';
import { alert, query, request } from '../common/Utils';

class ContactUs extends Component {
  state = {
    isSubmitting: false,
  }

  submitMessage = async (e) => {
    e.preventDefault();
    let contactForm = query('#contactForm')[0];

    if (!contactForm.checkValidity()) {
      e.stopPropagation()
      contactForm.classList.add('was-validated')
    } else {
      this.setState({ isSubmitting: true })

      let subject = query('#contactSubject')[0];
      let name = query('#contactName')[0];
      let email = query('#contactEmail')[0];
      let message = query('#contactMessage')[0];

      let body = new FormData();
      body.append('subject', subject.value);
      body.append('name', name.value);
      body.append('email', email.value);
      body.append('message', message.value);

      let resp = await request('/api/contactus', 'POST', body);

      if (resp.status === true) {
        alert('Thank you for reaching out, we would get back to you as soon as possible', 'success', false);
        subject.value = ""; name.value = ""; email.value = ""; message.value = "";
      } else {
        alert('Message not submitted, please try again or send us a mail at support@sportsfansng.com', 'error', false);
      }

      this.setState({ isSubmitting: false })
    }

  }

  render() {
    return (
      <>

        {/* Header */}
        <Header page='/contact'></Header>

        <section>
          {/* Title */}
          <div className='position-relative d-block'>
            <img className='' src={tennis} style={{ objectFit: 'cover', objectPosition: 'center', width: '100%', maxHeight: '200px' }} alt="" />
            <div className='position-absolute top-0 start-0 bg-white-50 h-100 w-100'>
              <h5 className="text-center position-absolute text-light top-50 start-50 translate-middle fw-bold fs-1">Contact&nbsp;Us</h5>
            </div>
          </div>
        </section>

        {/* Contact Us */}
        <section className="py-3 container">

          <div className='row'>

            <div className='col-lg-6'>
              <div className='p-lg-5 p-3'>
                <div className='lh-lg fs-5'>


                  <div className='fw -bold'>Get in Touch</div>
                  <h2 className='gravtrac fw-bold d-none d-lg-inline-block' style={{ fontSize: '3em' }}>
                    We will be glad to hear from you
                  </h2>

                  <div className='mb-3 mb-lg-4'>
                    <b>Email</b> <br />
                    <span>support&#64;sportsfansng.com</span>
                  </div>

                  <div className='mb-3 mb-lg-4'>
                    <b>Telephone</b> <br />
                    <span>+234 9093-590-559, +234 9093-590-559</span>
                  </div>

                  <div className='mb-3 mb-lg-4'>
                    <b>Address</b> <br />
                    <span>39, Aliu Bisiriyu Street, Ago Place Way, <br />Okota, Lagos.</span> <br />
                  </div>

                </div>
              </div>
            </div>

            <hr className='d-block d-sm-none col-10 mx-auto' />

            <div className='col-lg-6'>
              <div className='p-lg-5 p-1'>
                <form className='needs-validation' id='contactForm' onSubmit={(e) => this.submitMessage(e)} noValidate={true} >

                  <div className="form-floating mb-lg-5 mb-3">
                    <input type="text" disabled={this.state.isSubmitting} className="form-control" id="contactSubject" placeholder="Subject" required />
                    <label htmlFor="contactSubject">Subject<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter a subject.</div>
                  </div>

                  <div className="form-floating mb-lg-5 mb-3">
                    <input type="text" disabled={this.state.isSubmitting} className="form-control" id="contactName" placeholder="Name" required />
                    <label htmlFor="contactName">Name<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter your name.</div>
                  </div>

                  <div className="form-floating mb-lg-5 mb-3">
                    <input type="email" disabled={this.state.isSubmitting} className="form-control" id="contactEmail" placeholder="name@example.com" required />
                    <label htmlFor="contactEmail">Email address<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter a email address.</div>
                  </div>

                  {/* <label htmlFor="formFile" className="form-label">Upload a file</label> */}
                  {/* <div class="mb-4">
                    <input className="form-control form-control-lg" disabled={this.state.isSubmitting} type="file" id="formFile" />
                  </div> */}

                  <div className="form-floating mb-lg-5 mb-3">
                    <textarea className="form-control" disabled={this.state.isSubmitting} placeholder="Leave a comment here" id="contactMessage" style={{ height: '100px' }} defaultValue={""} required />
                    <label htmlFor="contactMessage">Message<span className='text-danger ms-1'>*</span></label>
                    <div className="invalid-feedback">Please enter a message.</div>
                  </div>

                  <div className='clearfix'>
                    <button className='float-end btn btn-lg btn-primary px-5' disabled={this.state.isSubmitting} type="submit">
                      {this.state.isSubmitting && <span className="spinner-border spinner-border border-2 spinner-border-sm me-3" role="status" aria-hidden="true"></span>}
                      {this.state.isSubmitting === true ? 'Submitting' : 'Submit'}
                    </button>
                  </div>

                </form>
              </div>
            </div>

          </div>

        </section>


        {/* Footer */}
        < Footer ></Footer>

      </>
    );
  }
}

export default ContactUs;