import React, { Component } from 'react';
import { usePaystackPayment } from 'react-paystack';
// import logo from '../../assets/imgs/badge.png';
import paystack from '../../assets/imgs/paystack.png';
import flutterwave from '../../assets/imgs/flutterwave.png';
import { AppContext } from '../../context/AppContext';
import { alert, formatDate, formatMoney, request } from '../../common/Utils';
import Toolbar from '../toolbar/Toolbar';


let token = '';
let role = '';

// you can call this function anything
function onPaystackSuccess(reference) {
  // Implementation for whatever you want to do with reference and after success call.
  verifyPayment(reference.trxref, token);
};

// you can call this function anything
function onPaystackClose() {
  // implementation for  whatever you want to do when the Paystack dialog closed.
  console.log('closed')
}

async function initiatePayment(reference, token, invoice, service) {
  let body = new FormData();
  body.append('reference', reference);
  body.append('invoicenumber', invoice);
  body.append('service', service);

  let headers = new Headers();
  headers.append('AuthToken', token);

  try {
    let resp = await request('/api/payment/initiate', 'POST', body, headers);
    if (resp.status === true) {
      return true;
    } else {
      return false;
    }
  } catch (error) {
    alert('Network Error', 'error');
    return false;
  }
}

async function verifyPayment(reference, token) {
  let body = new FormData();
  body.append('reference', reference);

  let headers = new Headers();
  headers.append('AuthToken', token);

  try {
    let resp = await request('/api/payment/verify', 'POST', body, headers);
    if (resp.status === true) {
      alert(resp.message, 'success');
      window.location.href = role === 'admin' ? "/admin/dashboard" : "/member/dashboard";
    } else {
      alert(resp.message, 'error');
      return false;
    }
  } catch (error) {
    alert('Network Error', 'error');
    return false;
  }
}

function PayWithPaystack(props) {
  let { email, invoice, amount, authtoken, authrole } = { ...props };
  token = authtoken; role = authrole;
  let reference = `${invoice}-${Math.floor(Math.random() * 99999)}`;

  let config = {
    reference: reference,
    email: email.toString(),
    amount: amount * 100, // convert the naira to kobo
    publicKey: 'pk_test_579a731077fa2885e1119ad97a783bca3c66fb8d',
  };

  const initializePayment = usePaystackPayment(config);
  return (
    <div>
      <button className='btn btn-muted w-100 h-100 border mt-1' title="Pay with Paystack" disabled={false} onClick={async () => {
        let initiate = await initiatePayment(reference, token, invoice, 'Paystack');
        if (initiate === true) {
          await initializePayment(onPaystackSuccess, onPaystackClose)
        } else {
          alert('Could not initate Paystack service.', 'error');
        }
      }}>
        <img src={paystack} className='img' width={200} alt="Pay with Paystack" />
      </button>
    </div>
  );
};

const PayWithFlutterwave = () => {
  // const initializePayment = usePaystackPayment();
  return (
    <div>
      <button className='btn btn-muted w-100 h-100 border mt-1 pt-2 pb-2' title="Pay with Flutterwave" disabled={true} onClick={() => {
        // initializePayment(onSuccess, onClose)
      }}>
        <img src={flutterwave} className='img' width={200} alt="Pay with Flutterwave" />
      </button>
    </div>
  );
};

class PayInvoice extends Component {
  static contextType = AppContext;

  state = {
    invoice: {},
    noUnpaidInvoice: false
  }

  getUnpaidInvoice = async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);

      let resp = await request('/api/invoice/unpaid', 'GET', null, headers);
      if (resp.status === true) {
        this.setState({
          invoice: resp.data
        });
      } else {
        this.setState({ noUnpaidInvoice: true })
      }
    } catch (error) {
      console.log(error);
    }
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['member', 'admin']);
    await Promise.all([
      this.getUnpaidInvoice()
    ]);
  }

  render() {
    return (
      <>
        <Toolbar props={this.props}></Toolbar>
        <section className='container my-3 px-3 text-secondary'>

          <div className='mb-3 text-dark'>
            <h1>Payment Invoice</h1>
            <hr className='my-0' />
          </div>

          <div className='mb-3 rounded border border-light shadow px-lg-4 px-3 pt-lg-5 pt-3 pb-5'>

            {
              this.state.noUnpaidInvoice === true ?
                <>
                  <div className='d-flex justify-content-center'>
                    <div className='fs-3'>
                      No Unpaid Invoice
                      </div>
                  </div>
                </>
                :
                <>
                  <div className='d-flex justify-content-between align-items-center'>
                    <div className='fs-2 text-primary'>Invoice #{this.state.invoice.invoice_number}</div>
                    <div>
                      {formatDate(this.state.invoice.created_at)} <br />
                      <div style={{ fontSize: '14px' }} className={this.state.invoice.status === 'unpaid' ? 'text-danger' : ''}>{this.state.invoice.status}</div>
                    </div>
                  </div>
                  <div>
                    <div>
                      <div className='fs-6'>{this.state.invoice.invoice_description}</div>
                      <div className='fs-6 mt-1 lh-0'>
                        Total:<span className='ms-1 fs-2'>{formatMoney(this.state.invoice.amount_due, 0)}</span>
                      </div>
                    </div>
                  </div>

                  <div className='mt-lg-3'>
                    <hr />
                      Complete payment with:

                      <div className='row mt-2'>
                      <div className='col-lg-6 col-md-6 col-12 mb-3'>
                        <PayWithPaystack email={this.context.auth.user.email} invoice={this.state.invoice.invoice_number} amount={this.state.invoice.amount_due} authtoken={this.context.auth.token} role={this.context.auth.role} />
                      </div>

                      <div className='col-lg-6 col-md-6 col-12 mb-3'>
                        <PayWithFlutterwave />
                      </div>

                    </div>

                  </div>
                </>
            }

          </div>

        </section>
      </>
    );
  }
}

export default PayInvoice;
