import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import logo from '../assets/imgs/badge.png';
import { AppContext } from '../context/AppContext';

class Header extends Component {
  static contextType = AppContext;

  constructor(props) {
    super(props);
    this.state = {};
  }


  render() {
    return (
      <div className="sticky-top">

        <div style={{ zIndex: 1 }} className='w-100 h-auto position-relative'>
          <div id="notify" className="position-relative w-100">
            {/* Alert */}
            <div id="alertContainer" style={{ zIndex: 1070 }} className="toast-container position-absolute top-0 end-0 mt-3 me-lg-3 me-1"></div>
          </div>
        </div>

        {/* Intro */}
        <header>
          <div className="">

            <nav className="sticky -top navbar navbar-expand-lg navbar-light bg-light">
              <div className="container">
                <Link className="navbar-brand" to="/">
                  <img src={logo} width={30} alt="" />
                  <span className='fw-bold ms-2 d-none d-md-inline-block text-primary align-middle'>SPORT FANS CLUB</span>
                </Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarTogglerDemo02" aria-controls="navbarTogglerDemo02" aria-expanded="false" aria-label="Toggle navigation">
                  <span className="navbar-toggler-icon" />
                </button>
                <div className="collapse navbar-collapse" id="navbarTogglerDemo02">
                  <ul className="navbar-nav m-auto mb-2 mb-lg-0">
                    <li className="nav-item">
                      <Link className={`nav-link fw-bold ${this.props.page === '/about' && 'active'}`} to="/about">ABOUT US</Link>
                    </li>
                    <li className="nav-item">
                      <Link className={`nav-link fw-bold ${this.props.page === '/how' && 'active'}`} to="/how">HOW IT WORKS</Link>
                    </li>
                    <li className="nav-item">
                      <Link className={`nav-link fw-bold ${this.props.page === '/benefits' && 'active'}`} to="/benefits">BENEFITS</Link>
                    </li>
                    <li className="nav-item">
                      <Link className={`nav-link fw-bold ${this.props.page === '/faqs' && 'active'}`} to="/faqs">FAQ</Link>
                    </li>
                    <li className="nav-item">
                      <Link className={`nav-link fw-bold ${this.props.page === '/contact' && 'active'}`} to="/contact">CONTACT US</Link>
                    </li>
                  </ul>
                  <form className="d-flex">
                    {
                      this.context.auth.authenticated === true && ['member', 'admin'].includes(this.context.auth.role) ?
                        <Link to={this.context.auth.role === 'member' ? '/member/dashboard' : '/admin/dashboard'} className="btn btn-primary" type="submit">Dashboard</Link>
                        : <>
                          <Link to='/login' className="btn btn-primary me-2" type="submit">Login</Link>
                          <Link to='/register' className="btn btn-primary" type="submit">Register</Link>
                        </>
                    }
                  </form>
                </div>
              </div>
              <div className='clearfix'></div>
            </nav>

          </div>
        </header>


      </div>

    );
  }

}

export default Header;