import React, { Component } from 'react';
import { Link } from 'react-router-dom';

// images
import Header from '../header/Header';
import Footer from '../footer/Footer';
import { parseUrlQuery, query, request, alert } from '../common/Utils';
import logo from '../assets/imgs/badge.png';
import ReCAPTCHA from 'react-google-recaptcha';

// SITE KEY
// 6LcIUsAaAAAAALojZ1S-6vNCWotec7BewFoad3Zk
// SECRET KEY
// 6LcIUsAaAAAAAHaJBZ8wQlu_Dh2WAL78g7ux2-0O  


class Register extends Component {
  state = {
    isSubmitting: false,
    showPassword: false,
    showConfirmPassword: false,
    slots: [],
    selectedSlot: 0,
    referralCode: ''
  }

  componentDidMount() {
    // get the slot details
    this.getSlots();

    let queryMap = parseUrlQuery();

    // selected slot
    this.setState({ selectedSlot: queryMap.slotId ?? 0 });
    // retrieve referral code
    this.setState({ referralCode: queryMap.referral ?? '' });
  }

  getSlots = async () => {
    try {
      let resp = await request('/api/slots');
      let slots = resp.status === true ? resp.data : [];
      this.setState({ slots: slots });
    } catch (error) {
      console.log(error);
    }
  }

  submitRegisteration = async (e) => {
    e.preventDefault();
    let registerationForm = query('#registerationForm')[0];

    if (!registerationForm.checkValidity()) {
      e.stopPropagation()
      alert('Please fill in all required inputs correctly', 'error');
      registerationForm.classList.add('was-validated')
    } else {

      this.setState({ isSubmitting: true })
      let email = query('#registerEmail')[0].value;
      let password = query('#registerPassword')[0].value;
      let cpassword = query('#registerConfirmPassword')[0].value;
      let referralcode = query('#registerReferralCode')[0].value;

      let body = new FormData();
      body.append('email', email);
      body.append('password', password);
      body.append('cpassword', cpassword);
      body.append('slot', this.state.selectedSlot);
      body.append('referralcode', referralcode);

      try {
        let resp = await request('/api/register', 'POST', body);

        if (resp.status === true) {
          alert('Your account have been created successfully, Your slot is being created.', 'success', true);

          setTimeout(async () => {
            // choose slot for member
            let body = new FormData();
            body.append('email', email);
            body.append('slot', this.state.selectedSlot);

            let slotresp = await request('/api/auto-choose-slot', 'POST', body);

            if (slotresp.status === true) {
              alert(slotresp['message'] + '<br />Your slots have been created successfully, Login to continue.', 'success', true);
              setTimeout(() => {
                this.props.history.push('/login');
              }, 5000);
            } else {
              alert('Your slot was not selected, Login to select the slot manually', 'error')
              setTimeout(() => {
                this.props.history.push('/login');
              }, 5000);
            }

            this.setState({ isSubmitting: false });
          }, 3000);

        } else {

          let errmsg = "";
          if (resp.data !== null) {
            resp.data.map(error => (
              // split the error message into a list
              errmsg += `<li>${error}</li>`
            ))
          } else errmsg = resp.message;
          alert(errmsg, 'error')
          this.setState({ isSubmitting: false })
        }
      } catch (error) {
        alert('Network Error', 'error', false)
        this.setState({ isSubmitting: false })
      }

    }

  }

  handleToken = (token) => {
    // setForm((currentForm) => {
    //   return { ...currentForm, token }
    // })
  }

  handleExpire = () => {
    // setForm((currentForm) => {
    //  return {...currentForm, token: null }
    // })
  }

  render() {
    return (
      <>

        {/* Header */}
        <Header></Header>

        {/* Register */}
        <section className="container my-5 d-flex justify-content-center">

          <div className='col-lg-6 col-md-10 col-12'>

            <div className='border rounded px-3 pt-3 pb-5'>
              <div className='text-center m-0'>
                <div className="fs-1">
                  <img src={logo} width={50} alt="" />
                </div>
              </div >

              <div className='text-center mb-3'>
                <div className='fw-light fs-1 d-lg-none'>Join Us</div>
                <div className='fw-light d-none d-lg-inline-block' style={{ fontSize: '3em' }}>Join Us</div>
                <div>Already have an account? <Link className='text-primary' to='/login'>Login</Link></div>
              </div>
              <form id='registerationForm' onSubmit={(e) => this.submitRegisteration(e)} noValidate={true} >

                <div className="form-floating mb-4">
                  <input type="email" disabled={this.state.isSubmitting} className="form-control" id="registerEmail" placeholder="name@example.com" required />
                  <label htmlFor="registerEmail">Email address<span className='text-danger ms-1'>*</span></label>
                  <div className="invalid-feedback">Please enter an email address.</div>
                </div>

                <div className="form-floating mb-1 clearfix">
                  <input type={this.state.showPassword === true ? "text" : "password"} disabled={this.state.isSubmitting} className="form-control" id="registerPassword" placeholder='******' required />
                  <label htmlFor="registerPassword">Password<span className='text-danger ms-1'>*</span></label>
                  <div className="fw-bold btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                    this.setState({ showPassword: !this.state.showPassword });
                  }
                  }>{this.state.showPassword === true ? 'Hide Password' : 'Show Password'}</div>
                  <div className="invalid-feedback">Please enter a secured password.</div>
                </div>

                <div className="form-floating mb-1 clearfix">
                  <input type={this.state.showConfirmPassword === true ? "text" : "password"} disabled={this.state.isSubmitting} className="form-control" id="registerConfirmPassword" placeholder='******' required />
                  <label htmlFor="registerConfirmPassword">Confirm Password<span className='text-danger ms-1'>*</span></label>
                  <div className="fw-bold btn btn-muted text-muted rounded-0 mt-1 px-0 py-1 float-end" style={{ fontSize: '12px' }} onClick={() => {
                    this.setState({ showConfirmPassword: !this.state.showConfirmPassword });
                  }
                  }>{this.state.showConfirmPassword === true ? 'Hide Password' : 'Show Password'}</div>
                  <div className="invalid-feedback">Please enter a secured password.</div>
                </div>

                <label className="ms-3">
                  Select a Slot<span className='text-danger ms-1'>*</span> <br />
                  <span className='fw-bold'>{
                    this.state.selectedSlot > 0
                      ?
                      `${this.state.selectedSlot} Slot${this.state.selectedSlot > 1 ? 's' : ''}`
                      :
                      'No slot selected'
                  }</span>
                </label>

                <div className='d-flex justify-content-start overflow-auto mb-4'>

                  {
                    this.state.slots.map((slot, index) => (

                      <div key={index} className={`col-lg-4 col-5 card shadow-sm ${index > 0 && 'ms-lg-3 ms-1'} me-lg-3 me-1 my-3`}>

                        <input disabled={this.state.isSubmitting} type="radio" name="registerSlot" defaultChecked={this.state.selectedSlot === slot.id} className="btn-check" id={'registerSlot' + index} autoComplete="off" />
                        <label className="btn w-100 h-100 btn-outline-secondary" onClick={() => this.setState({ selectedSlot: slot.id })} htmlFor={'registerSlot' + index}>

                          <div className='fs-6 fw-bold text-center'>
                            {slot.program} <br />
                            <span className='fs-6 text -muted'>{slot.id} Slot{slot.id > 1 ? 's' : ''}</span>
                          </div>

                        </label>

                      </div>

                    ))
                  }

                </div>

                <div className="form-floating mb-4">
                  <input type="text" pattern='[a-z0-9]{7}' maxLength='7' disabled={this.state.isSubmitting} className="form-control" id="registerReferralCode" placeholder='1234567' defaultValue={this.state.referralCode} />
                  <label htmlFor="registerReferralCode">Referral Code</label>
                  <div className="invalid-feedback">Please enter a valid referral code.</div>
                </div>

                <div className="form-check mb-4">
                  <input style={{ backgroundColor: '#000087' }} className="form-check-input" type="checkbox" id="registerTermsAndCondition" required disabled={this.state.isSubmitting} />
                  <label className="form-check-label" htmlFor="registerTermsAndCondition">I agree to the <Link className='text-primary' to='/terms'>Terms and Conditions</Link></label>
                  <div className="invalid-feedback">Please accept our terms and conditions.</div>
                </div>

                {/* reCAPTCHA */}
                <ReCAPTCHA
                  sitekey="6LcIUsAaAAAAALojZ1S-6vNCWotec7BewFoad3Zk"
                  onChange={() => this.handleToken}
                  onExpire={() => this.handleExpire}
                  className="mb-3"
                ></ReCAPTCHA>

                <button className='w-100 btn btn-lg btn-primary px-5' disabled={this.state.isSubmitting} type="submit">
                  {
                    this.state.isSubmitting === true
                      ? <div>
                        <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                        <span className='ms-2 align-middle'>Creating your account...</span>
                      </div>
                      : <span className='ms-2 align-middle'>Register</span>}
                </button>

              </form>
            </div >

          </div >

        </section >

        {/* Footer */}
        < Footer ></Footer >

      </>
    );
  }
}

export default Register;