import { faCheck, faExclamation } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { alert, formatDate, formatMoney, request } from '../../common/Utils';
import { AppContext } from '../../context/AppContext';
import Layout from '../layout/Layout';


class MemberBenefits extends Component {
  static contextType = AppContext;

  state = {
    mybenefits: []
  }

  getMyBenefits = async () => {
    // get my benefits
    try {
      // alert('HI');
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/my-benefits', 'GET', null, headers);

      if (resp.status === true) {
        this.setState({ mybenefits: resp.data });
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  queryBenefits = async () => {
    // get my benefits
    try {
      // alert('HI');
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/querybenefits', 'POST', null, headers);

      if (resp.status === true) {
        alert('Queried successfully', 'success');
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['member', 'admin']);
    await this.getMyBenefits();
  }

  render() {
    return (
      <>
        <Layout props={this.props} page='/benefits'>
          <section className='container my-3 px-3 text-secondary'>

            <div className='mb-2 text-dark'>
              <h1 className='lh-0 my-0'>Your Benefits</h1>
              {this.state.mybenefits.length > 0 && <span>You have accrued {this.state.mybenefits.length} benefits</span>}
              <hr className='my-0' />
            </div>

            <div>
              {
                this.state.mybenefits.length === 0 &&
                <div className='my-5 py-5 d-flex flex-column align-items-center'>
                  <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                  No Accrued Benefits
                </div>
              }
            </div>

            <div className='text-secondary mt-4'>
              {
                this.state.mybenefits.map((benefit, index) => (

                  <div key={index} className='mb-1 rounded border border-light shadow-sm px-lg-4 px-3 py-3'>
                    <div className='d-flex justify-content-between align-items-center'>
                      <div className='fw-bold lh-0 text-primary'>{benefit.achievement}</div>
                      <div style={{ fontSize: '12px' }}>{formatDate(benefit.created_at)}</div>
                    </div>

                    <div className='fs-6 lh-0 mt-3'>
                      {
                        benefit.benefit === null ? "No souvenir" : benefit.benefit
                      }
                    </div>

                    <div className='d-flex justify-content-between align-items-center'>
                      <span className='fs-5'>{formatMoney(benefit.cash, 0)}</span>
                      <div>
                        {
                          benefit.status === 'pending' &&
                          <button className='btn btn-sm pt-0 pb-1 btn-primary' onClick={this.queryBenefits}>query</button>
                        }
                        {
                          benefit.status === 'given' &&
                          <>
                            <div className='text-success' style={{ fontSize: '14px' }}>
                              given <FontAwesomeIcon icon={faCheck}></FontAwesomeIcon> <br />
                              {formatDate(benefit.updated_at)}
                            </div>
                          </>
                        }
                        {
                          benefit.status === 'cancelled' &&
                          <>
                            <div className='text-danger' style={{ fontSize: '14px' }}>
                              cancelled <br />
                              {formatDate(benefit.updated_at)}
                            </div>
                          </>
                        }
                      </div>

                    </div>
                  </div>

                ))
              }
            </div>

          </section>
        </Layout>
      </>
    );
  }

}

export default MemberBenefits;