import React, { Component } from 'react';
import { faAngleUp, faGlobeAfrica, faPencilAlt, faPlus, faTrashAlt } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { alert, query, request } from '../../common/Utils';
import { AppContext } from '../../context/AppContext';
import Layout from '../../members/layout/Layout';

class AdminCompetitions extends Component {
  static contextType = AppContext;

  constructor(props) {
    super(props);
    this.state = {
      competitions: [],
      isEditing: false,
      currentCompetition: {},
      currentCompetitionId: 0,
      currentCompetitionQualifiedTeam: [],
      availableSports: [
        "Football", "American Football", "Baseball", "Basketball", "Car Racing", "Boxing", "Swimming", "Tennis", "Golf", "Racing", "Hockey"
      ],
      slotpackages: [],
      selectedSlot: "1",
      countries: [],

      region: '',
      sport: 'Football',
      filteredCompetitions: []
    }
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['admin']);
    await Promise.all([
      this.getCompetitions(),
      this.getSlotPackages(),
      this.getCountries()
    ]);
    // await this.getRechargePins();
  }

  getSlotPackages = async () => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/admin/slots', 'GET', null, headers);

      if (resp.status === true) {
        this.setState({
          slotpackages: resp.data
        });
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  getCountries = async () => {
    // get countries
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/getcountries', 'GET', null, headers);

      if (resp.status === true) {
        this.setState({
          countries: resp.data
        });
      }
    } catch (error) {
      console.log(error);
    }
  }

  getCompetitions = async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/admin/competitions', "GET", null, headers)

      if (resp.status === true) {
        this.setState({ competitions: resp.data, filteredCompetitions: resp.data });
        this.setCurrentCompetition(resp.data[0]);
      }
    } catch (error) {
      alert('Network Error', 'error')
    }
  }

  filter = () => {
    let region = query('#region')[0].value;
    let sport = query('#sport')[0].value ?? "Football";
    let competitions = this.state.competitions
      .filter((competition) => { return competition.sport === sport; })
      .filter((competition) => { return region === "" ? true : competition.region === region; })
    this.setState({ filteredCompetitions: competitions });
    this.setCurrentCompetition(competitions[0]);
  }

  setCurrentCompetition = (competition) => {
    if (competition === undefined)
      this.setState({ currentCompetition: {}, currentCompetitionId: 0, currentCompetitionQualifiedTeam: [] })
    else
      this.setState({ currentCompetition: competition, currentCompetitionId: competition.id, currentCompetitionQualifiedTeam: JSON.parse(competition.qualified_teams) });
  }

  renderCompetitions = () => {
    let competitions = this.state.filteredCompetitions

    if (competitions.length === 0)
      return <div>
        No Competitions.
      </div>

    return <ul className="list-group">
      {
        competitions.map((competition, index) => (
          <li key={index} style={{ cursor: 'pointer' }} className={`list-group-item ${competition.id === this.state.currentCompetitionId && 'bg-primary text-light'}`} onClick={() => this.setCurrentCompetition(competition)}>
            <div className='fw-bold'>{competition.region}</div>
            <small>{competition.competition}</small>
          </li>
        ))
      }
    </ul>
  }

  renderCompetitionQualifiedTeams = () => {

    if (this.state.currentCompetitionQualifiedTeam.length === 0)
      return <div>
        No Qualified Teams.
    </div>

    let list = [];
    this.state.slotpackages.forEach((slot) => { list.push(slot.program); })

    return <>

      <div className="p-2 rounded border mb-3">
        <h6>{this.state.currentCompetition.competition}</h6>
        <small>
          <FontAwesomeIcon icon={faGlobeAfrica}></FontAwesomeIcon> {this.state.currentCompetition.region}
          <span className='mx-1 fw-bold'></span>
          <b>
            {this.state.currentCompetition.sport}
          </b>
          <span className='mx-1 fw-bold'></span>
          <b>
            {list[this.state.currentCompetition.slotpackage - 1]} Slot Package
          </b>
        </small>
      </div>

      <ul className="list-group">
        {
          this.state.currentCompetitionQualifiedTeam.map((team, index) => (
            <li key={index} className="list-group-item">
              <div class="d-flex justify-content-between align-items-start">
                <div class="fw-bold">{index + 1}.<span className='ms-lg-4'>{team}</span></div>
              </div>
            </li>
          ))
        }
      </ul>
    </>
  }

  addCompetitionAndQualifiedTeamsModal = () => {
    return (
      <div>
        <div className="modal fade" id="addCompetitionAndQualifiedTeamsModal" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="addCompetitionAndQualifiedTeamsModalLabel" aria-hidden="true">
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="addCompetitionAndQualifiedTeamsModalLabel">{this.state.isEditing === true ? 'Edit' : 'Add'} Competition</h5>
                <button id="addCompetitionAndQualifiedTeamsModalDismissBtn" type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
              </div>
              <form onSubmit={this.state.isEditing === true ? this.updateCompetition : this.addCompetition}>
                <div className="modal-body">

                  <div className="">
                    <h6>Choose Slot Package</h6>
                    <div className="row mb-3 mx-1">
                      {
                        this.state.slotpackages.map((slot, index) => (

                          <div key={index} className={`col-lg-2 col-6 border border-secondary p-0`}>

                            <input disabled={this.state.isSubmitting} type="radio" name="slotpackage" defaultChecked={this.state.selectedSlot === slot.id} className="btn-check" id={'slotpackage' + index} autoComplete="off" />
                            <label className={`btn rounded-0 ${this.state.selectedSlot === slot.id && 'btn-secondary'} btn-muted w-100`} onClick={() => this.setState({ selectedSlot: slot.id })} htmlFor={'slotpackage' + index}>

                              <div className='fs-6 text-center'>
                                {slot.program}
                              </div>

                            </label>

                          </div>

                        ))
                      }
                    </div>
                  </div>

                  <div className="mb-3">
                    <label htmlFor="modalsport" className='h6'>Select Sport</label>
                    <select className="form-select form-control-lg" id="modalsport" placeholder="Sport">
                      {
                        this.state.availableSports.map((sport, key) => (
                          <option key={key} value={sport}>{sport}</option>
                        ))
                      }
                    </select>
                  </div>

                  <div className="mb-3">
                    <label htmlFor="modalregion" className='h6'>Select Region</label>
                    <select className="form-select form-control-lg" id="modalregion" placeholder="Region">
                      <option value="World Wide">World Wide</option>
                      <optgroup label="Continental">
                        {
                          ["Asia", "Europe", "Africa", "Oceania", "North America", "Antarctica", "South America"].map((region, key) => (
                            <option key={key} value={region}>{region}</option>
                          ))
                        }
                      </optgroup>
                      <optgroup label="National">
                        {
                          this.state.countries.map((region, key) => (
                            <option key={key} value={region.country}>{region.country}</option>
                          ))
                        }
                      </optgroup>

                    </select>
                  </div>

                  <div className="mb-3">
                    <h6>Competition</h6>
                    <input id="modalcompetition" type="text" class="form-control" placeholder="Competition" />
                  </div>

                  <div className="mg-3">
                    <h6>Qualified Teams</h6>

                    <div id="qualifiedTeamList">
                      <div className="input-group mb-2">
                        <input name="qualifiedTeam[]" type="text" className="form-control" placeholder="Qualified Team" />
                        <span style={{ cursor: 'pointer' }} onClick={(e) => { e.target.parentElement.outerHTML = ''; }} className="input-group-text px-lg-3 alert-danger">-</span>
                      </div>
                    </div>
                  </div>

                  <div className="clearfix">
                    <button type="button" className="float-end btn btn-sm btn-outline-secondary" onClick={
                      () => {
                        query("#qualifiedTeamList")[0].insertAdjacentHTML('beforeEnd', `
                        <div class="input-group mb-2">
                          <input name="qualifiedTeam[]" type="text" class="form-control" placeholder="Qualified Team" />
                          <span style="cursor: pointer;" onClick="this.parentElement.outerHTML = '';" class="input-group-text px-lg-3 alert-danger">-</span>
                        </div>
                        `);
                      }
                    }>
                      <FontAwesomeIcon icon={faPlus}></FontAwesomeIcon>
                    </button>
                  </div>

                </div>
                <div className="modal-footer">
                  <button type="reset" className="btn btn-danger px-5" onClick={
                    () => {
                      query("#qualifiedTeamList")[0].innerHTML = `
                        <div class="input-group mb-2">
                          <input name="qualifiedTeam[]" type="text" class="form-control" placeholder="Qualified Team" />
                          <span style="cursor: pointer;" onClick="this.parentElement.outerHTML = '';" class="input-group-text px-lg-3 alert-danger">-</span>
                        </div>
                      `;
                    }
                  }>Reset</button>
                  <button type="submit" className="btn btn-primary px-5">{this.state.isEditing === true ? 'Update' : 'Save'}</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }

  addCompetition = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    let sport = query("#modalsport")[0];
    let region = query("#modalregion")[0];
    let competition = query("#modalcompetition")[0];
    let qualifiedTeams = [];
    query("input[name='qualifiedTeam[]']").forEach(function (elem) {
      qualifiedTeams.push(elem.value);
    });

    let body = new FormData();
    body.append('slotpackage', this.state.selectedSlot)
    body.append('sport', sport.value);
    body.append('region', region.value);
    body.append('competition', competition.value);
    body.append('qualified_teams', JSON.stringify(qualifiedTeams));

    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/admin/competitions/create', 'POST', body, headers);

      if (resp.status === true) {
        alert(`Competition added successfully`, 'success');
        this.setState({ competitions: resp.data, filteredCompetitions: resp.data })
        this.setCurrentCompetition(resp.data[0]);
        query("#addCompetitionAndQualifiedTeamsModalDismissBtn")[0].click();
      } else {
        let errmsg = "";
        if (resp.data !== null) {
          resp.data.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;
        alert(errmsg, 'error')
      }
    } catch (error) {
      alert('Network Error<br />' + error, 'error')
    }
  }

  editCompetition = () => {
    this.setState({ isEditing: true, selectedSlot: this.state.currentCompetition.slotpackage });
    query('#modalsport')[0].value = this.state.currentCompetition.sport;
    query('#modalcompetition')[0].value = this.state.currentCompetition.competition;
    query('#modalregion')[0].value = this.state.currentCompetition.region;
    query("#qualifiedTeamList")[0].innerHTML = '';
    console.log(JSON.parse(this.state.currentCompetition.qualified_teams));
    JSON.parse(this.state.currentCompetition.qualified_teams).forEach((team) => {
      query("#qualifiedTeamList")[0].innerHTML += `
        <div class="input-group mb-2">
          <input name="qualifiedTeam[]" type="text" class="form-control" placeholder="Qualified Team" value="${team}" />
          <span style="cursor: pointer;" onClick="this.parentElement.outerHTML = '';" class="input-group-text px-lg-3 alert-danger">-</span>
        </div>
      `;
    });
    query("#addCompetitionAndQualifiedTeamsModalTrigger")[0].click();
  }

  updateCompetition = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    let sport = query("#modalsport")[0];
    let region = query("#modalregion")[0];
    let competition = query("#modalcompetition")[0];
    let qualifiedTeams = [];
    query("input[name='qualifiedTeam[]']").forEach(function (elem) {
      qualifiedTeams.push(elem.value);
    });

    let body = new FormData();
    body.append('competitionId', this.state.currentCompetitionId);
    body.append('slotpackage', this.state.selectedSlot)
    body.append('sport', sport.value);
    body.append('region', region.value);
    body.append('competition', competition.value);
    body.append('qualified_teams', JSON.stringify(qualifiedTeams));

    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/admin/competitions/update', 'POST', body, headers);

      if (resp.status === true) {
        alert(`Competition updated successfully`, 'success');
        this.setState({ competitions: resp.data, filteredCompetitions: resp.data, isEditing: false })
        this.setCurrentCompetition(resp.data[0]);
        query("#addCompetitionAndQualifiedTeamsModalDismissBtn")[0].click();
      } else {
        let errmsg = "";
        if (resp.data !== null) {
          resp.data.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;
        alert(errmsg, 'error')
      }
    } catch (error) {
      alert('Network Error<br />' + error, 'error')
    }
  }

  deleteCompetition = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let body = new FormData();
      body.append('competitionId', this.state.currentCompetitionId);
      let resp = await request('/api/admin/competitions/delete', "POST", body, headers)

      if (resp.status === true) {
        this.setState({ competitions: resp.data });
        this.setCurrentCompetition(resp.data[0]);
      }
    } catch (error) {
      alert('Network Error', 'error')
    }
  }

  render() {
    return (
      <>
        <Layout props={this.props} page='/competitions'>
          <section className='container my-3 px-3'>

            <div className='mb-3'>
              <div className='d-flex justify-content-between align-items-center pb-2 pb-lg-0'>
                <h1 className='lh-0 my-0'>Competitions</h1>
                <button className="btn btn-sm btn-outline-secondary" id="addCompetitionAndQualifiedTeamsModalTrigger" data-bs-toggle="modal" data-bs-target="#addCompetitionAndQualifiedTeamsModal">
                  <FontAwesomeIcon icon={faPlus}></FontAwesomeIcon>
                  <span className="ms-2 d-md-inline-block d-none">ADD COMPETITION</span>
                </button>
              </div>
              <hr className='my-0' />
            </div>

            <div className="mb-lg-4 mb-2">
              <label htmlFor="sport" className='h6'>
                Filter by sport
              </label>
              <select onChange={this.filter} className="form-select form-control-lg" id="sport" placeholder="Sport">
                {
                  this.state.availableSports.map((sport, key) => (
                    <option key={key} value={sport}>{sport}</option>
                  ))
                }
              </select>
            </div>

            <div className="">

              <div className="row">
                <div className="col-lg-3 col-md-5 mb-3">
                  <div className="">

                    <h5>Competitions</h5>

                    <div className='mt-0 mt-lg-4 w-100 mb-3'>
                      <div className='mb-1'>
                        <FontAwesomeIcon icon={faGlobeAfrica}></FontAwesomeIcon>
                        <span className='ms-1'>Filter by region</span>
                      </div>
                      <select onChange={this.filter} className="form-select form-control-lg" id="region" placeholder="Region">
                        <option value="">All</option>
                        <option value="World Wide">World Wide</option>
                        <optgroup label="Continental">
                          {
                            ["Asia", "Europe", "Africa", "Oceania", "North America", "Antarctica", "South America"].map((region, key) => (
                              <option key={key} value={region}>{region}</option>
                            ))
                          }
                        </optgroup>
                        <optgroup label="National">
                          {
                            this.state.countries.map((region, key) => (
                              <option key={key} value={region.country}>{region.country}</option>
                            ))
                          }
                        </optgroup>

                      </select>
                    </div>

                    <div className="mt-">
                      {this.renderCompetitions()}
                    </div>

                  </div>
                </div>

                <div className="col-lg-9 col-md-7">
                  <div className="">

                    <div className='mb-3 d-flex justify-content-between align-items-center'>
                      <h5>Competition and Qualified Teams</h5>

                      <div className='d-flex'>
                        <button className="btn btn-sm btn-danger me-1" onClick={this.deleteCompetition}>
                          <FontAwesomeIcon icon={faTrashAlt}></FontAwesomeIcon>
                        </button>
                        <button className="btn btn-sm btn-outline-secondary" onClick={this.editCompetition}>
                          <FontAwesomeIcon icon={faPencilAlt}></FontAwesomeIcon>
                          <span className="ms-2 d-md-inline-block d-none">UPDATE</span>
                        </button>
                      </div>

                    </div>

                    <div className="">
                      {this.renderCompetitionQualifiedTeams()}
                    </div>

                  </div>
                </div>

              </div>

            </div>

            {this.addCompetitionAndQualifiedTeamsModal()}

            <div className="clearfix mb-5">
              <a className="float-end btn btn-muted d-md-none d-inline-block" href="#top">
                <FontAwesomeIcon icon={faAngleUp}></FontAwesomeIcon>
              </a>
            </div>
          </section>
        </Layout>
      </>
    );
  }

}

export default AdminCompetitions;