import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { alert, formatDate, formatMoney, formatNumber, query, request } from '../../common/Utils';
import { AppContext } from '../../context/AppContext';
import Layout from '../../members/layout/Layout';
import { Link } from 'react-router-dom';
import { faEdit, faExclamation, faPlus } from '@fortawesome/free-solid-svg-icons';


class AdminReferralLevels extends Component {
  static contextType = AppContext;

  state = {
    referrallevels: [],
    editingLevel: {}
  }

  getReferralLevels = async () => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/admin/referrallevels', 'GET', null, headers);

      if (resp.status === true) {
        this.setState({
          referrallevels: resp.data
        });
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['admin']);
    await this.getReferralLevels();
  }

  addReferralLevelModal = () => {
    return (
      <div>
        <div className="modal fade" id="addReferralLevelModal" tabIndex={-1} aria-labelledby="addReferralLevelModalLabel" aria-hidden="true">
          <div className="modal-dialog modal-dialog- centered">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="addReferralLevelModalLabel">Editing {this.state.editingLevel.rank} Rank</h5>
                <button id="addReferralLevelModalDismissBtn" type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
              </div>
              <form onSubmit={this.updateReferralLevel}>
                <div className="modal-body">
                  <div className="mb-3">
                    <label htmlFor="referralrank" className="form-label">Rank</label>
                    <input type="text" readOnly={true} autoComplete={""} className="form-control" id="referralrank" placeholder="Referral Level Rank" />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="referralrequired" className="form-label">No. of referrals required</label>
                    <input type="number" className="form-control form-control" id="referralrequired" placeholder="no. of referrals required" />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="referralcashbenefit" className="form-label">Cash Benefit</label>
                    <input type="" autoComplete={""} className="form-control" id="referralcashbenefit" placeholder="Cash benefit" />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="referralsouvenirbenefit" className="form-label">Cash Benefit</label>
                    <textarea id="referralsouvenirbenefit" cols="30" rows="5" autoComplete={""} className="form-control" placeholder="Souvenir benefit"></textarea>
                  </div>
                </div>
                <div className="modal-footer">
                  <button type="submit" className="btn btn-primary px-5">Save</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }

  editReferralLevel(referrallevel) {
    this.setState({ editingLevel: referrallevel, isEditing: true });
    query('#referralrank')[0].value = referrallevel.rank;
    query('#referralrequired')[0].value = referrallevel.referrals_required;
    query('#referralcashbenefit')[0].value = referrallevel.cash_benefit;
    query('#referralsouvenirbenefit')[0].value = referrallevel.benefits;
    query('#addReferralLevelModalToggle')[0].click();
  }

  noReferralLevels = () => {
    return <tr>
      <td colSpan={7} className='text-center'>No Referral Levels.</td>
    </tr>
  }

  renderReferralLevels = () => {
    return this.state.referrallevels.map((referrallevel, index) => (
      <tr key={index}>
        <td>{index + 1}</td>
        <td>{referrallevel.rank}</td>
        <td>{formatNumber(referrallevel.referrals_required)} persons</td>
        <td>{formatMoney(referrallevel.cash_benefit, 0)}</td>
        <td>{referrallevel.benefits}</td>
        <td>{formatDate(referrallevel.updated_at) ?? ""}</td>
        <td>
          <FontAwesomeIcon className="mx-2" title={`edit ${referrallevel.rank}`} style={{ cursor: "pointer" }} icon={faEdit} onClick={() => this.editReferralLevel(referrallevel)}></FontAwesomeIcon>
        </td>
      </tr>
    ))
  }

  updateReferralLevel = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    let rank = query('#referralrank')[0];
    let referrals_required = query('#referralrequired')[0];
    let cash_benefit = query('#referralcashbenefit')[0];
    let benefits = query('#referralsouvenirbenefit')[0];

    let body = new FormData();
    body.append('id', this.state.editingLevel.id);
    body.append('rank', rank.value);
    body.append('referrals_required', referrals_required.value);
    body.append('cash_benefit', cash_benefit.value);
    body.append('benefits', benefits.value);

    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/admin/referrallevels/update', 'POST', body, headers);

      if (resp.status === true) {
        alert(`Referral level updated successfully`, 'success');
        this.setState({ referrallevels: resp.data })
        query("#addReferralLevelModalDismissBtn")[0].click();
        referrals_required.value = ""; cash_benefit.value = ""; benefits.value = "";
      } else {
        let errmsg = "";
        if (resp.data !== null) {
          resp.data.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;
        alert(errmsg, 'error')
      }
    } catch (error) {
      alert('Network Error<br />' + error, 'error')
    }
  }

  render() {
    return (
      <>
        <Layout props={this.props} page='/referral-levels'>
          <section className='container my-3 px-3'>

            <div className='mb-3'>
              <h1 className='lh-0 my-0'>Referral Levels</h1>
              <hr className='my-0' />
            </div>
            <button id='addReferralLevelModalToggle' className="d-none btn btn-sm btn-outline-secondary" data-bs-toggle="modal" data-bs-target="#addReferralLevelModal"></button>

            <div>
              {
                this.state.referrallevels.length === 0 &&
                <div className='my-5 py-5 d-flex flex-column align-items-center'>
                  <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                  There are no Referral Levels
                </div>
              }
            </div>

            {this.state.referrallevels.length > 0 &&
              <>
                <div className="table-responsive">
                  <table className="table table-striped table-sm">
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Rank</th>
                        <th>Referrals&nbsp;Required&nbsp;/&nbsp;Slot</th>
                        <th>Cash&nbsp;Benefit</th>
                        <th>Souvenir&nbsp;Benefits</th>
                        <th>Date&nbsp;Updated</th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.referrallevels.length > 0 ? this.renderReferralLevels() : this.noReferralLevels()}
                    </tbody>
                  </table>
                </div>
              </>
            }

            {this.addReferralLevelModal()}


          </section>
        </Layout>
      </>
    );
  }

}

export default AdminReferralLevels;