import { faExclamation } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { formatDate, request } from '../../common/Utils';
import { AppContext } from '../../context/AppContext';
import Layout from '../../members/layout/Layout';


class AdminNotifications extends Component {
  static contextType = AppContext;

  state = {
    unreadNotifications: []
  }

  getUnreadNotifications = async () => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/unread-admin-notifications', 'GET', null, headers);

      if (resp.status === true) {
        this.setState({ unreadNotifications: resp.data });
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  openNotification = async (notice) => {
    // mark notification as read
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let body = new FormData();
      body.append('notificationId', notice.id)
      let resp = await request('/api/mark-as-read', 'POST', body, headers);

      if (resp.status === true && notice.route != null) {
        this.context.auth.user.notifications -= 1;
        // if user is admin, route
        if (this.context.auth.role === 'admin') {
          notice.route = notice.route.replace('member', 'admin');
        }
        this.props.history.push(notice.route);
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['admin']);
    await this.getUnreadNotifications();
  }

  render() {
    return (
      <>
        {/* <div className='btn btn-sm btn-primary m-0 py-0 px-2' style={{ fontSize: '14px' }}>Mark as read</div> */}
        <Layout props={this.props} page='/admin-notifications'>
          <section className='container my-3 px-3'>

            <div className='mb-2'>
              <h1 className='lh-0 my-0'>Admin Notifications</h1>
              {this.state.unreadNotifications.length > 0 && <span>You have {this.state.unreadNotifications.length} unread notifications</span>}
              <hr className='my-0' />
            </div>

            <div>
              {
                this.state.unreadNotifications.length === 0 &&
                <div className='my-5 py-5 d-flex flex-column align-items-center'>
                  <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                  No Unread Notifications
                </div>
              }
            </div>

            <div className='text-secondary mt-4'>
              {

                this.state.unreadNotifications.map((notice, index) => (

                  <div key={index} className='mb-1 rounded border border-light shadow-sm px-lg-4 px-3 py-3' style={{ cursor: 'pointer' }} onClick={() => this.openNotification(notice)}>
                    <div className='d-flex justify-content-between align-items-top'>
                      <div className='lh-0'>{notice.message === null ? "No message" : notice.message}</div>
                    </div>

                    <div className='mt-2' style={{ fontSize: '14px' }}>{formatDate(notice.created_at)}</div>

                  </div>

                ))

              }
            </div>

          </section>
        </Layout>
      </>
    );
  }

}

export default AdminNotifications;