import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { alert, formatDate, formatMoney, request } from '../../common/Utils';
import { AppContext } from '../../context/AppContext';
import Layout from '../../members/layout/Layout';
import { faCircle, faExclamation } from '@fortawesome/free-solid-svg-icons';


class AdminPayments extends Component {
  static contextType = AppContext;

  state = {
    payments: [],
    editingLevel: {}
  }

  getpayments = async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/admin/payments', 'GET', null, headers);

      if (resp.status === true) {
        this.setState({
          payments: resp.data
        });
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  queryPayment = async (reference) => {
  
    let headers = new Headers();
    headers.append('AuthToken', this.context.auth.token);
    
    let body = new FormData();
    body.append('reference', reference);
  
    try {
      let resp = await request('/api/payment/verify', 'POST', body, headers);
      if (resp.status === true) {
        alert(resp.message, 'success');
        await this.getpayments();
      } else {
        alert(resp.message, 'error')
      }
    } catch (error) {
      alert('Query failed', 'error')
      console.log(error);
    }
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['admin']);
    await this.getpayments();
  }

  nopayments = () => {
    return <tr>
      <td colSpan={9} className='text-center'>No Payments.</td>
    </tr>
  }

  renderpayments = () => {
    return this.state.payments.map((payment, index) => (
      <tr key={index}>
        <td>
          <span className={`text-secondary ${payment.domain === 'live' && 'text-success'} ${payment.domain === 'test' && 'text-danger'}`} ><FontAwesomeIcon icon={faCircle}></FontAwesomeIcon></span>
          &nbsp;{index + 1}
        </td>
        <td>#{payment.invoice_number}</td>
        <td>{payment.email}</td>
        <td>{formatMoney(payment.amount_paid / 100, 0)}</td>
        <td>{payment.ip_address}</td>
        <td>{payment.payment_service}</td>
        <td>{payment.gateway_response}</td>
        <td>{payment.status}</td>
        <td>
          {
            payment.status === 'success'
              ? formatDate(payment.paid_at)
              : <button className='btn btn-sm pt-0 pb-1 btn-primary' onClick={() => this.queryPayment(payment.reference)}>query</button>
          }
        </td>
      </tr>
    ))
  }

  render() {
    return (
      <>
        <Layout props={this.props} page='/payments'>
          <section className='container my-3 px-3'>

            <div className='mb-3'>
              <h1 className='lh-0 my-0'>Payments</h1>
              <hr className='my-0' />
            </div>
            <button id='addReferralLevelModalToggle' className="d-none btn btn-sm btn-outline-secondary" data-bs-toggle="modal" data-bs-target="#addReferralLevelModal"></button>

            <div>
              {
                this.state.payments.length === 0 &&
                <div className='my-5 py-5 d-flex flex-column align-items-center'>
                  <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                  There are no Referral Levels
                </div>
              }
            </div>

            {this.state.payments.length > 0 &&
              <>
                <div className="table-responsive">
                  <table className="table table-striped table-sm">
                    <thead>
                      <tr>
                        <th className='ps-3'>#</th>
                        <th>Invoice&nbsp;Number</th>
                        <th>Email</th>
                        <th>Amount&nbsp;Paid</th>
                        <th>IP&nbsp;Address</th>
                        <th>Service</th>
                        <th>Gateway&nbsp;Response</th>
                        <th>Status</th>
                        <th>Date&nbsp;Paid</th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.payments.length > 0 ? this.renderpayments() : this.nopayments()}
                    </tbody>
                  </table>
                </div>
              </>
            }

          </section>
        </Layout>
      </>
    );
  }

}

export default AdminPayments;