import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { alert, formatDate, formatMoney, formatNumber, query, request } from '../../common/Utils';
import { AppContext } from '../../context/AppContext';
import Layout from '../../members/layout/Layout';
import { Link } from 'react-router-dom';
import { faEdit, faExclamation, faPlus } from '@fortawesome/free-solid-svg-icons';


class AdminSlotPackges extends Component {
  static contextType = AppContext;

  state = {
    slotpackages: [],
    editingLevel: {}
  }

  getSlotPackages = async () => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/admin/slots', 'GET', null, headers);

      if (resp.status === true) {
        this.setState({
          slotpackages: resp.data
        });
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['admin']);
    await this.getSlotPackages();
  }

  addSlotPackageModal = () => {
    return (
      <div>
        <div className="modal fade" id="addSlotPackageModal" tabIndex={-1} aria-labelledby="addSlotPackageModalLabel" aria-hidden="true">
          <div className="modal-dialog modal-dialog- centered">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="addSlotPackageModalLabel">Editing {this.state.editingLevel.rank} Slot Package</h5>
                <button id="addSlotPackageModalDismissBtn" type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
              </div>
              <form onSubmit={this.updateSlotPackage}>
                <div className="modal-body">
                  <div className="mb-3">
                    <label htmlFor="slotprogram" className="form-label">Rank</label>
                    <input type="text" readOnly={true} autoComplete={""} className="form-control" id="slotprogram" placeholder="Slot Package" />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="no_slots" className="form-label">No. of slots</label>
                    <input type="number" className="form-control form-control" id="no_slots" placeholder="no. of slots" />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="cost" className="form-label">Cash Benefit</label>
                    <input type="" autoComplete={""} className="form-control" id="cost" placeholder="Cash benefit" />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="slotbenefits" className="form-label">Slot Benefit</label>
                    <textarea id="slotbenefits" cols="30" rows="5" autoComplete={""} className="form-control" placeholder="Souvenir benefit"></textarea>
                  </div>
                </div>
                <div className="modal-footer">
                  <button type="submit" className="btn btn-primary px-5">Save</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }

  editSlotPackage(slotpackage) {
    this.setState({ editingLevel: slotpackage, isEditing: true });
    query('#slotprogram')[0].value = slotpackage.program;
    query('#no_slots')[0].value = slotpackage.no_slots;
    query('#cost')[0].value = slotpackage.cost;
    query('#slotbenefits')[0].value = slotpackage.benefits;
    query('#addSlotPackageModalToggle')[0].click();
  }

  noSlotPackages = () => {
    return <tr>
      <td colSpan={7} className='text-center'>No Slot Packages.</td>
    </tr>
  }

  renderSlotPackages = () => {
    return this.state.slotpackages.map((slotpackage, index) => (
      <tr>
        <td>{index + 1}</td>
        <td>{slotpackage.program}</td>
        <td>{formatNumber(slotpackage.no_slots)} slots</td>
        <td>{formatMoney(slotpackage.cost, 0)}</td>
        <td>{slotpackage.benefits}</td>
        <td>{formatDate(slotpackage.updated_at) ?? ""}</td>
        <td>
          <FontAwesomeIcon className="mx-2" title={`edit ${slotpackage.rank}`} style={{ cursor: "pointer" }} icon={faEdit} onClick={() => this.editSlotPackage(slotpackage)}></FontAwesomeIcon>
        </td>
      </tr>
    ))
  }

  updateSlotPackage = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    let rank = query('#slotprogram')[0];
    let referrals_required = query('#no_slots')[0];
    let cash_benefit = query('#cost')[0];
    let benefits = query('#slotbenefits')[0];

    let body = new FormData();
    body.append('id', this.state.editingLevel.id);
    body.append('program', rank.value);
    body.append('no_slots', referrals_required.value);
    body.append('cost', cash_benefit.value);
    body.append('benefits', benefits.value);

    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/admin/slots/update', 'POST', body, headers);

      if (resp.status === true) {
        alert(`Slot package updated successfully`, 'success');
        this.setState({ slotpackages: resp.data })
        query("#addSlotPackageModalDismissBtn")[0].click();
        referrals_required.value = ""; cash_benefit.value = ""; benefits.value = "";
      } else {
        let errmsg = "";
        if (resp.data !== null) {
          resp.data.map(error => (
            // split the error message into a list
            errmsg += `<li>${error}</li>`
          ))
        } else errmsg = resp.message;
        alert(errmsg, 'error')
      }
    } catch (error) {
      alert('Network Error<br />' + error, 'error')
    }
  }

  render() {
    return (
      <>
        <Layout props={this.props} page='/packages'>
          <section className='container my-3 px-3'>

            <div className='mb-3'>
              <h1 className='lh-0 my-0'>Slot Packages</h1>
              <hr className='my-0' />
            </div>
            <button id='addSlotPackageModalToggle' className="d-none btn btn-sm btn-outline-secondary" data-bs-toggle="modal" data-bs-target="#addSlotPackageModal"></button>

            <div>
              {
                this.state.slotpackages.length === 0 &&
                <div className='my-5 py-5 d-flex flex-column align-items-center'>
                  <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                  There are no Slot Packages
                </div>
              }
            </div>

            {this.state.slotpackages.length > 0 &&
              <>
                <div className="table-responsive">
                  <table className="table table-striped table-sm">
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Slot&nbsp;Package</th>
                        <th>No.&nbsp;of&nbsp;Slots</th>
                        <th>Cost</th>
                        <th>Benefits</th>
                        <th>Date&nbsp;Updated</th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.slotpackages.length > 0 ? this.renderSlotPackages() : this.noSlotPackages()}
                    </tbody>
                  </table>
                </div>
              </>
            }

            {this.addSlotPackageModal()}


          </section>
        </Layout>
      </>
    );
  }

}

export default AdminSlotPackges;