import { faArrowLeft, faServer } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { formatMoney, formatNumber, parseUrlQuery, request } from '../../common/Utils';
import { AppContext } from '../../context/AppContext';
import Layout from '../../members/layout/Layout';

class AdminMemberSlots extends Component {
  static contextType = AppContext;

  state = {
    referrallevels: [],
    mypackage: {},
    myslots: [],
    member: "",
    paymentRequired: false
  }

  getReferralLevels = async () => {
    try {
      let resp = await request('/api/referrallevels');
      let referrallevels = resp.status === true ? resp.data : [];
      this.setState({ referrallevels: referrallevels });
    } catch (error) {
      console.log(error);
    }
  }

  getMyPackageAndSlots = async (email) => {
    // get my package and slots
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let body = new FormData();
      body.append('email', email);
      let resp = await request('/api/admin/memberpackage', 'POST', body, headers);
      if (resp.status === true) {
        this.setState({ mypackage: resp.data });
        if (this.state.mypackage.status === 'pending') {
          this.setState({ paymentRequired: true });
        }

        let slotsresp = await request('/api/admin/users/slots', 'POST', body, headers);
        if (slotsresp.status === true) {
          this.setState({ myslots: slotsresp.data });
        }
      }
    } catch (error) {
      // alert('Error retrieving slot')
      console.log(error);
    }
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['admin']);
    let query = parseUrlQuery();
    if (query.email !== undefined) {
      this.setState({ member: query.email });
      await Promise.all([
        this.getReferralLevels(),
        this.getMyPackageAndSlots(query.email)
      ]);
    } else {
      this.props.history.goBack()
    }

  }

  renderSlots = (level) => {

    // do not render manager
    if (level.rank === "Manager") return;

    let slotsInThisLevel = this.state.myslots.filter((slot) => { return slot.referral_level === level.rank });

    return (
      <div className='text-secondary mt-4'>
        <h4 className='ms-lg-2 ms-2'>{level.rank}</h4>
        {
          slotsInThisLevel.map((slot, index) => (

            <div key={index} className='mb-3 rounded border border-light shadow px-lg-4 px-3 py-lg-5 py-3'>
              <div className='d-flex justify-content-between align-items-top'>
                <div className='fw-bold text-primary'>{formatNumber(slot.referrals_required)} referrals required{slot.status === 'completed' && ' '}</div>
                <div className='' style={{ fontSize: '14px' }}>
                  {slot.referrals_acquired}/{slot.referrals_required}
                </div>
              </div>
              <div className="progress mt-3">
                <div className={`progress-bar progress-bar-striped ${slot.status === 'pending' ? 'bg-secondary' : 'bg-primary'} ${slot.status === 'active' && 'progress-bar-animated'}`} role="progressbar" style={{ width: `${Math.ceil(((slot.referrals_acquired) / slot.referrals_required) * 100)}%` }}
                  aria aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">{Math.floor((slot.referrals_acquired / slot.referrals_required) * 100)}%</div>
              </div>
              <div className='d-flex justify-content-between '>
                <div className='fs-5'>{formatMoney(level.cash_benefit, 0)}</div>
                <div style={{ fontSize: '14px' }}>{slot.status}</div>
              </div>
            </div>

          ))
        }
      </div>
    )
  }

  render() {
    return (
      <>
        <Layout props={this.props} page=''>
          <section className='container my-3 px-3'>

            <div className='mb-2'>
              <h1 className='lh-0 my-0'>Member Slots</h1>
              {this.state.member} have {this.state.mypackage.no_slots} slots
              <hr className='my-0' />
              <div className='fw-normal d-flex align-items-center py-0 my-0 fs-6' style={{ height: '30px' }}>
                <FontAwesomeIcon icon={faServer} className='text-primary me-1'></FontAwesomeIcon>
                <div className=''>{this.state.mypackage.slot_program} Slot Package</div>
              </div>
              <button className='btn btn-sm btn-muted' onClick={() => { this.props.history.goBack() }}>
                <FontAwesomeIcon icon={faArrowLeft}></FontAwesomeIcon><span className='ms-1'>Back</span>
              </button>
            </div>

            <div>
              {
                this.state.referrallevels.map((level, index) => (
                  <div key={index}>
                    {this.renderSlots(level)}
                  </div>
                ))
              }
            </div>

          </section>
        </Layout>
      </>
    );
  }
}

export default AdminMemberSlots;