import { faAngleDown, faAngleRight, faArrowLeft } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { baseUrl, parseUrlQuery, request } from '../../common/Utils';
import { AppContext } from '../../context/AppContext';
import Layout from '../../members/layout/Layout';
import defaultProfilePicture from '../../assets/imgs/default_profile_picture.png';
import logo from '../../assets/imgs/badge.png';

class AdminAllDownlines extends Component {
  static contextType = AppContext;

  state = {
    isOrg: true,
    downlines: [],
    curDownline: [],
    curMember: {},
    downlineStack: [],
    curLevel: 'Manager'
  }

  getDownlines = async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);

      let query = parseUrlQuery();
      console.log(query.email)
      let resp = null;
      if (query.email === undefined) {
        this.setState({ isOrg: true });
        resp = await request('/api/admin/org-downlines', 'GET', null, headers);
      } else {
        this.setState({ isOrg: false });
        resp = await request('/api/admin/user-downlines?email=' + query.email, 'GET', null, headers);
      }
      if (resp.status === true) {
        if (this.state.isOrg === true) {
          this.setState({
            downlines: resp.data, curDownline: resp.data,
            curMember: {
              firstname: "SPORTS FANS CLUB",
              lastname: "",
              telephone: "No number",
              downline: resp.data,
              profile_picture: logo,
            }
          });
        } else {
          this.setState({
            downlines: resp.data.downlines, curDownline: resp.data.downlines,
            curMember: {
              firstname: resp.data.user.firstname,
              lastname: resp.data.user.lastname,
              telephone: resp.data.user.telephone,
              profile_picture: resp.data.user.profile_picture,
              downline: resp.data.downlines,
            }
          });
        }
      }

    } catch (error) {
      console.log(error);
    }
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['admin']);
    await this.getDownlines();
  }

  formatLevel = (level) => {
    switch (level) {
      case 1:
        return 'Coach';
      case 2:
        return 'Players';
      case 3:
        return 'Supporters';
      case 4:
        return 'Stadium';
      case 5:
        return 'Fans';
      case 6:
        return 'Trophy';
      default:
        return 'Manager';
    }
  }

  setCurrentDownline = (member) => {
    if (this.state.curMember != null) this.state.downlineStack.push(this.state.curMember);
    this.setState({ curDownline: member.downline, curMember: member, curLevel: this.formatLevel(member.level) })
  }

  setPrevDownline = () => {
    if (this.state.downlineStack.length === 0) {
      this.setState({ curDownline: this.state.downlines, curMember: null, curLevel: 'Manager' });
      return;
    }

    let member = this.state.downlineStack.pop();
    this.setState({ curDownline: member.downline, curMember: member, curLevel: this.formatLevel(member.level) })
  }

  render() {
    console.log('render');
    return (
      <>
        <Layout props={this.props} page='/alldownlines'>
          <section className='my-3 px-3 text-secondary'>

            <div className='mb-3 text-dark'>

              <h1>Downlines</h1>
              <hr className='my-0' />
              <small>Current Level: {this.state.isOrg === true ? 'Organization' : this.state.curLevel}</small>
              {
                this.state.isOrg === false &&
                <>
                  <br />
                  <button className='btn btn-sm btn-muted' onClick={() => { this.props.history.goBack() }}>
                    <FontAwesomeIcon icon={faArrowLeft}></FontAwesomeIcon><span className='ms-1'>Back</span>
                  </button>
                </>
              }
            </div>

            {/* Downlines */}
            <div className='my-3'>
              {
                // this.state.downlines.length === 0 ? 'Your downline is empty' :
                <div>
                  {
                    <div className='mx-3 mb-3 rounded border border-light shadow px-lg-4 px-3 py-3'>
                      <div className='d-flex justify-content-between align-items-center' style={{ cursor: 'pointer' }} onClick={this.state.downlineStack.length > 0 ? this.setPrevDownline : () => { }} >
                        <div className='d-flex align-items-center'>
                          {
                            this.state.downlineStack.length > 0 &&
                            // return to previous
                            <div className='me-3'>
                              <FontAwesomeIcon size='1x' icon={faArrowLeft}></FontAwesomeIcon>
                            </div>
                          }

                          {/* the profile picture */}
                          <div className='me-lg-4 me-3'>
                            {
                              this.state.isOrg === true
                                && this.state.downlineStack.length === 0 ?
                                // this.state.curMember === null ?
                                <img id='profilePicturePreview' className='rounded-circle' width='50px' height='50px' src={logo} alt="" />
                                :
                                <img id='profilePicturePreview' className='rounded-circle' width='55px' height='55px' src={this.state.curMember.profile_picture != null ? `${baseUrl}/${this.state.curMember.profile_picture}` : defaultProfilePicture} alt="" />
                            }
                          </div>

                          {/* the names */}
                          <div>
                            <div className='h-100 d-flex flex-column justify-content-evenly align-items-start'>
                              {
                                <>
                                  <div>
                                    {this.state.curMember.firstname === null && this.state.curMember.lastname === null ? 'No name' : `${this.state.curMember.firstname} ${this.state.curMember.lastname}`}
                                  </div>
                                  <div>{this.state.curMember.telephone ?? 'No number'}</div>
                                </>
                              }
                            </div>
                          </div>
                        </div>

                        {/* clickable indication */}
                        <div>
                          <FontAwesomeIcon size='2x' icon={faAngleDown}></FontAwesomeIcon>
                        </div>

                      </div>
                    </div>
                  }
                  <div className='border-start ms-4 border-3 my-3 pt-2 pb-1 border-secondary'>
                    {
                      this.state.curDownline.length > 0 ?
                        this.state.curDownline.map((member, index) => (
                          <div key={index} className='mx-3 mb-3 rounded border border-light shadow px-lg-4 px-3 py-3'>
                            <div className='d-flex justify-content-between align-items-center' style={{ cursor: 'pointer' }} onClick={member.downline != null && this.state.curLevel !== 'Trophy' ? (() => this.setCurrentDownline(member)) : () => { }} >
                              <div className='d-flex'>
                                {/* the profile picture */}
                                <div className='me-lg-4 me-3'>
                                  <img id='profilePicturePreview' className='rounded-circle' width='50px' height='50px' src={member.profile_picture != null ? `${baseUrl}/${member.profile_picture}` : defaultProfilePicture} alt="" />
                                </div>

                                {/* the names */}
                                <div>
                                  <div className='h-100 d-flex flex-column justify-content-evenly align-items-start'>
                                    <div>
                                      {member.firstname === null && member.lastname === null ? 'No name' : `${member.firstname} ${member.lastname}`}
                                    </div>
                                    <div>{member.telephone || 'No number'}</div>
                                  </div>
                                </div>
                              </div>

                              {
                                member.downline.length > 0 &&
                                // clickable indication
                                <div>
                                  <FontAwesomeIcon size='2x' icon={faAngleRight}></FontAwesomeIcon>
                                </div>
                              }

                            </div>
                          </div>
                        ))
                        :
                        <div className='mx-3 mb-3 rounded border border-light shadow px-lg-4 px-3 py-3'>
                          There is no downline for this member.
                        </div>
                    }
                  </div>
                </div>
              }
            </div>

          </section>
        </Layout>
      </>
    );
  }
}

export default AdminAllDownlines;