import { faUsers, faCrown, faEdit, faExclamation, faInfoCircle, faServer, faShieldAlt } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { alert, baseUrl, formatDate, query, request } from '../../common/Utils';
import { AppContext } from '../../context/AppContext';
import Layout from '../../members/layout/Layout';
import defaultProfilePicture from '../../assets/imgs/default_profile_picture.png';
import { Link } from 'react-router-dom';


class AdminUsers extends Component {
  static contextType = AppContext;

  state = {
    allUsers: [],
    members: [],
    admins: [],
    limit: 10,
    membersPage: 1,
    viewMembers: [],
    searchMembers: [],
    memberPrivilege: {
      id: 0,
      firstname: '',
      role: '',
      privileges: []
    },
    memberNotice: {
      id: 0,
      firstname: '',
      isGroup: false
    }
  }

  getAllUsers = async () => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/admin/all-users', 'GET', null, headers);

      if (resp.status === true) {
        let allUsers = resp.data;
        this.setState({
          allUsers: allUsers,
          members: allUsers.filter((user) => { user.selected = false; return user.role === 'member' }),
          admins: allUsers.filter((user) => { user.selected = false; return user.role === 'admin' }),
        });
        this.setState({ viewMembers: this.state.members });
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  updateStatus = async (e, member, index) => {
    e.preventDefault();
    e.stopPropagation();
    try {
      let status = query(`#status${index}`)[0];
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);

      let body = new FormData();
      body.append('userId', member.id);
      body.append('status', status.value);
      let resp = await request('/api/admin/update-status', 'POST', body, headers);

      if (resp.status === true) {
        alert('Status updated successfully', 'success');
        this.getAllUsers();
      } else {
        alert('Status updated failed', 'error');
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  handleMemberSearch = (e) => {
    e.preventDefault();
    e.stopPropagation();

    let search = query('#memberSearchbox')[0];
    let searchmembers = this.state.members.filter((member) => {

      if (`${member.firstname != null ? member.firstname.toLowerCase() : ''} ${member.lastname != null ? member.lastname.toLowerCase() : ''} ${member.middlename != null ? member.middlename.toLowerCase() : ''}`.includes(search.value.toLowerCase())) return member;

      if (member.email === search.value) return member;

      if (member.referral_code === search.value) return member;

      if (member.member_id === search.value) return member;


      return false;

    });

    this.setState({ viewMembers: searchmembers, searchMembers: searchmembers });
  }

  addReferralLevelModal = () => {
    return (
      <div>
        <div className="modal fade" id="addReferralLevelModal" tabIndex={-1} aria-labelledby="addReferralLevelModalLabel" aria-hidden="true">
          <div className="modal-dialog modal-dialog- centered">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="addReferralLevelModalLabel">Editing {this.state.editingLevel.rank} Rank</h5>
                <button id="addReferralLevelModalDismissBtn" type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
              </div>
              <form onSubmit={this.updateReferralLevel}>
                <div className="modal-body">
                  <div className="mb-3">
                    <label htmlFor="referralrank" className="form-label">Rank</label>
                    <input type="text" readOnly={true} autoComplete={""} className="form-control" id="referralrank" placeholder="Referral Level Rank" />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="referralrequired" className="form-label">No. of referrals required</label>
                    <input type="number" className="form-control form-control" id="referralrequired" placeholder="no. of referrals required" />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="referralcashbenefit" className="form-label">Cash Benefit</label>
                    <input type="" autoComplete={""} className="form-control" id="referralcashbenefit" placeholder="Cash benefit" />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="referralsouvenirbenefit" className="form-label">Cash Benefit</label>
                    <textarea id="referralsouvenirbenefit" cols="30" rows="5" autoComplete={""} className="form-control" placeholder="Souvenir benefit"></textarea>
                  </div>
                </div>
                <div className="modal-footer">
                  <button type="submit" className="btn btn-primary px-5">Save</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }

  editPrivilege = (id, firstname, role, privileges) => {
    this.setState({
      memberPrivilege: {
        id: id,
        firstname: firstname,
        role: role,
        privileges: JSON.parse(privileges) ?? []
      }
    });
    query("#editPrivilegeModalTrigger")[0].click();
  }

  updatePrivilege = async (e, member, index) => {
    e.preventDefault();
    e.stopPropagation();
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);

      let body = new FormData();
      body.append('userId', this.state.memberPrivilege.id);
      body.append('role', this.state.memberPrivilege.role);
      body.append('privileges', JSON.stringify(this.state.memberPrivilege.privileges));
      let resp = await request('/api/admin/update-privileges', 'POST', body, headers);

      if (resp.status === true) {
        query('#editPrivilegeModalDismissBtn')[0].click();
        alert(resp.message, 'success');
        this.getAllUsers();
      } else {
        alert(resp.message, 'error');
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  editPrivilegeModal = () => {
    return (
      <>
        <button className="btn btn-sm btn-outline-secondary d-none" id="editPrivilegeModalTrigger" data-bs-toggle="modal" data-bs-target="#editPrivilegeModal"></button>
        <div>
          <div className="modal fade" id="editPrivilegeModal" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="editPrivilegeModalLabel" aria-hidden="true">
            <div className="modal-dialog modal-lg">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="editPrivilegeModalLabel">Edit {this.state.memberPrivilege.firstname}'s Privileges</h5>
                  <button id="editPrivilegeModalDismissBtn" type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
                </div>
                <form onSubmit={this.updatePrivilege}>
                  <div className="modal-body">

                    <div className="mb-3">
                      <h6>Role</h6>

                      <div className="btn-group w-100">
                        {
                          ['member', 'admin'].map((role, index) => (
                            <div key={index} className={`btn p-0 btn-outline-secondary ${this.state.memberPrivilege.role === role && 'btn-secondary text-light'}`}>
                              <input disabled={this.state.isSubmitting} type="radio" name="role" defaultChecked={this.state.memberPrivilege.role === role} className="btn-check" id={'role' + index} autoComplete="off" />
                              <label className="w-100" onClick={() => this.setState({ memberPrivilege: { ...this.state.memberPrivilege, role: role } })} htmlFor={'role' + index}>
                                <div className='fs-6 p-2 text-center'>
                                  {role.toUpperCase()}
                                </div>
                              </label>
                            </div>
                          ))
                        }
                      </div>
                    </div>

                    <div className="mb-3">
                      <h6>Privileges</h6>

                      <div className="row">
                        {
                          [
                            'users', 'downlines', 'invoices', 'payments', 'competitions', 'slot packages',
                            'referral levels', 'member benefits', 'notifications'].map((privilege, index) => (
                              <div key={index} className="col-lg-4 col-md-6 col-12 mb-3 form-check form-switch p-0">
                                <input disabled={this.state.isSubmitting} type="checkbox" checked={this.state.memberPrivilege.privileges.includes(privilege) === true} id={'privilege' + index} autoComplete="off" className='form-check-input ms-2 ms-lg-3 me-2' value={privilege} />
                                <label onClick={() => {
                                  console.log(this.state.memberPrivilege.privileges.includes(privilege))
                                  if (!this.state.memberPrivilege.privileges.includes(privilege)) {
                                    this.state.memberPrivilege.privileges.push(privilege);
                                    this.setState({
                                      memberPrivilege: {
                                        ...this.state.memberPrivilege,
                                        privileges: this.state.memberPrivilege.privileges
                                      }
                                    });
                                  } else {
                                    this.state.memberPrivilege.privileges.splice(this.state.memberPrivilege.privileges.indexOf(privilege), 1);
                                    this.setState({
                                      memberPrivilege: {
                                        ...this.state.memberPrivilege,
                                        privileges: this.state.memberPrivilege.privileges
                                      }
                                    });
                                  }
                                }} htmlFor={'privilege' + index}>
                                  <div className='fs-6'>
                                    {privilege.toUpperCase()}
                                  </div>
                                </label>
                              </div>
                            ))
                        }
                      </div>

                    </div>

                  </div>
                  <div className="modal-footer">
                    <button type="submit" className="btn btn-primary px-5">Update</button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }

  selectAdmin = (selected) => {
    let selectedMember = this.state.admins.find((member) => { return member.id === selected.id });
    selectedMember['selected'] = !selectedMember.selected ?? true;
    this.setState({ members: this.state.members });
  }

  selectMember = (selected) => {
    let selectedMember = this.state.members.find((member) => { return member.id === selected.id });
    selectedMember['selected'] = !selectedMember.selected ?? true;
    this.setState({ members: this.state.members });
  }

  sendNotice = async (e) => {
    e.preventDefault();
    e.stopPropagation();
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);

      let resp;
      if (this.state.memberNotice.isGroup === true) {
        let selected = [];
        this.state.members.filter(member => member.selected === true).forEach(i => selected.push(i.id));
        let body = new FormData();
        body.append('userIds', JSON.stringify(selected));
        body.append('message', query('#noticeMessage')[0].value);
        resp = await request('/api/admin/send-group-notice', 'POST', body, headers);
      } else {
        let body = new FormData();
        body.append('userId', this.state.memberNotice.id);
        body.append('message', query('#noticeMessage')[0].value);
        resp = await request('/api/admin/send-notice', 'POST', body, headers);
      }

      if (resp.status === true) {
        query('#sendNoticeModalDismissBtn')[0].click();
        alert("Notice sent", 'success');
      } else {
        alert('Notice not sent', 'error');
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  sendSingleNotice = (id, firstname) => {
    this.setState({
      memberNotice: {
        id: id,
        firstname: firstname,
        isGroup: false
      }
    });
    query("#sendNoticeModalTrigger")[0].click();
  }

  sendNoticeToMany = (group) => {
    this.setState({
      memberNotice: {
        firstname: group,
        isGroup: true
      }
    });
    query("#sendNoticeModalTrigger")[0].click();
  }

  sendNoticeModal = () => {
    return (
      <>
        <button className="btn btn-sm btn-outline-secondary d-none" id="sendNoticeModalTrigger" data-bs-toggle="modal" data-bs-target="#sendNoticeModal"></button>
        <div>
          <div className="modal fade" id="sendNoticeModal" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="sendNoticeModalLabel" aria-hidden="true">
            <div className="modal-dialog modal-lg">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="sendNoticeModalLabel">Send Notice to {this.state.memberNotice.firstname}</h5>
                  <button id="sendNoticeModalDismissBtn" type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
                </div>
                <form onSubmit={this.sendNotice}>
                  <div className="modal-body">

                    <div className="mb-3">
                      <h6>Message</h6>

                      <div className="">
                        <textarea className="form-control" disabled={this.state.isSubmitting} placeholder="Leave a comment here" id="noticeMessage" style={{ height: '100px' }} defaultValue={""} required />
                      </div>

                    </div>

                  </div>
                  <div className="modal-footer">
                    <button type="submit" className="btn btn-primary px-5">Send</button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['admin']);
    await this.getAllUsers();
  }

  render() {
    return (
      <>
        <Layout props={this.props} page='/users'>
          <section className='container my-3 px-3'>

            <div className='mb-5'>
              <h1 className='lh-0 my-0'>Accounts</h1>
              <span>There are a total of {this.state.allUsers.length} accounts</span>
            </div>

            <div>
              {
                this.state.allUsers.length === 0 &&
                <div className='my-5 py-5 d-flex flex-column align-items-center'>
                  <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                  There are no Accounts
                </div>
              }
            </div>

            {this.state.allUsers.length > 0 &&
              <>

                <div className='mb-5'>
                  <div className='mb-4 row'>
                    <div className='col-12 col-lg-6 mb-lg-0 mb-2'>
                      <h4 className='lh-0 my-0'>Administrators</h4>
                      <small>{this.state.admins.length > 1 ? `There are ${this.state.admins.length} Administrators accounts` : `There is only one Administrator`}</small>
                    </div>

                    <div className='col-12 col-lg-6 mb-lg-0 mb-2 clearfix'>
                      <button disabled={
                        this.state.admins.every((admin) => { return admin.selected === false })
                      } className='btn btn-sm btn-primary float-end' onClick={() => { this.setState({ membersPage: this.state.membersPage - 1 }) }}>
                        <FontAwesomeIcon className='text-light' icon={faInfoCircle}></FontAwesomeIcon> Notify Selected Admins
                      </button>
                    </div>
                    {/* <hr className='my-0' /> */}
                  </div>

                  <div>
                    {
                      this.state.admins.length === 0 &&
                      <div className='my-5 py-5 d-flex flex-column align-items-center'>
                        <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                      There are no Administrators
                    </div>
                    }
                  </div>

                  {this.state.admins.length > 0 &&
                    <>
                      <div className='table-responsive w-100'>
                        <table className='table table-hover table- primary'>
                          <thead>
                            <tr>
                              <th>#</th>
                              <th>Admin</th>
                              <th>Slot&nbsp;Package</th>
                              <th>Rank</th>
                              <th>Email</th>
                              <th>Telephone</th>
                              <th>Registered</th>
                              <th>Status</th>
                              <th></th>
                            </tr>
                          </thead>

                          <tbody>
                            {/* .slice(((this.state.membersPage * this.state.limit) - this.state.limit), (this.state.membersPage * this.state.limit)) */}
                            {this.state.admins.map((member, index) => (
                              <tr key={index}>
                                <td className='align-middle'>
                                  <div className="form-check">
                                    <input onChange={() => this.selectAdmin(member)} checked={member.selected} className="form-check-input" type="checkbox" id={`admin${index}`} />
                                    <label className="form-check-label" htmlFor={`admin${index}`}>{index + 1 + (this.state.limit * (this.state.membersPage - 1))}</label>
                                  </div>
                                </td>
                                <td className='align-middle'>
                                  <div className='align-top'>
                                    <div className='d-flex'>
                                      <div className='me-2' onClick={() => { this.props.history.push('/admin/users/profile?u=' + member.email) }} style={{ cursor: 'pointer' }} title={`Click to view ${member.firstname ?? 'No name'}'s profile`}>
                                        <img id='profilePicturePreview' className='rounded-circle' width='50px' height='50px' src={member.profile_picture != null ? `${baseUrl}/${member.profile_picture}` : defaultProfilePicture} alt="" />
                                      </div>
                                      <div>
                                        {
                                          member.firstname === null && member.lastname === null && member.middlename === null
                                            ? <span>No&nbsp;name</span> :
                                            <span>{member.firstname}&nbsp;{member.lastname}&nbsp;{member.middlename}</span>
                                        } <br />
                                        <small>{member.member_id ?? <span>No&nbsp;member&nbsp;ID</span>}<span className='text-dark fw-bold mx-1'></span>{member.referral_code ?? 'No referral code'}</small>
                                      </div>
                                    </div>
                                  </div>
                                </td>
                                <td className='align-middle'>
                                  {member.slot_program} <br />
                                  <small>({member.no_slots}&nbsp;{member.no_slots > 1 ? 'slots' : 'slot'})</small>
                                </td>
                                <td className='align-middle'>
                                  {member.rank}
                                </td>
                                <td className='align-middle'>
                                  {member.email}
                                </td>
                                <td className='align-middle'>
                                  {member.telephone ?? <span>No&nbsp;telephone</span>}
                                </td>
                                <td className='align-middle'>
                                  {formatDate(member.created_at)}
                                </td>
                                <td className='align-middle'>
                                  <div style={{ width: '100px' }} className='d-flex align-items-center'>
                                    <div>{member.verification_status}</div>
                                    <FontAwesomeIcon style={{ cursor: 'pointer' }} onClick={() => {
                                      let editstatus = query(`#editadminstatus${index}`)[0];
                                      editstatus.hidden = !editstatus.hidden;
                                    }} className='text-primary ms-2' title={`Update ${member.firstname ?? 'No name'}'s status`} icon={faEdit}></FontAwesomeIcon>
                                  </div>

                                  <form id={`editadminstatus${index}`} hidden={true} onSubmit={(e) => this.updateStatus(e, member, index)}>
                                    <div className="form-floating mt-2">
                                      <select className="form-control" id={`adminstatus${index}`} defaultValue={member.verification_status}>
                                        <option value="verified">verified</option>
                                        <option value="blocked">blocked</option>
                                        <option value="closed">closed</option>
                                      </select>
                                      <label htmlFor={`adminstatus${index}`} className=''>select&nbsp;status</label>
                                      <button type='submit' className='btn mt-2 w-100 btn-sm btn-primary'>update</button>
                                    </div>
                                  </form>

                                </td>
                                <td className='align-middle'>
                                  <div className='d-flex'>
                                    <button onClick={() => this.sendSingleNotice(member.id, member.firstname)} className='btn btn-sm btn-muted px-' title={`Send notice to ${member.firstname ?? 'No name'}`}><FontAwesomeIcon className='text-primary' icon={faInfoCircle}></FontAwesomeIcon></button>

                                    <Link to={`/admin/users/downlines?email=${member.email}`} className='btn btn-sm btn-muted' title={`View ${member.firstname ?? 'No name'}'s downlines`}><FontAwesomeIcon className='text-primary' icon={faUsers}></FontAwesomeIcon></Link>

                                    <Link to={`/admin/users/slots?email=${member.email}`} className='btn btn-sm btn-muted' title={`View ${member.firstname ?? 'No name'}'s slots`}><FontAwesomeIcon className='text-primary' icon={faServer}></FontAwesomeIcon></Link>

                                    <Link to={`/admin/users/benefits?email=${member.email}`} className='btn btn-sm btn-muted' title={`View ${member.firstname ?? 'No name'}'s benefits`}><FontAwesomeIcon className='text-primary' icon={faCrown}></FontAwesomeIcon></Link>

                                    <button onClick={() => this.editPrivilege(member.id, member.firstname, member.role, member.permissions)} className='btn btn-sm btn-muted' title={`Edit ${member.firstname ?? 'No name'}'s privileges`}><FontAwesomeIcon className='text-primary' icon={faShieldAlt}></FontAwesomeIcon></button>
                                  </div>
                                </td>
                              </tr>
                            ))
                            }
                          </tbody>

                        </table>
                      </div>
                    </>
                  }
                </div>

                <div className='mb-5'>
                  <div className='mb-4 row'>
                    <div className='col-12 col-lg-6 mb-lg-0 mb-2'>
                      <h4 className='lh-0 my-0'>Members</h4>
                      <small>{this.state.members.length > 1 ? `There are ${this.state.members.length} member accounts` : `There is only one Member`}</small>
                    </div>

                    <div className='col-12 col-lg-6'>
                      <form onSubmit={e => e.preventDefault()}>
                        <input type="search" id="memberSearchbox" onChange={this.handleMemberSearch} className='form-control form-control w-100' placeholder='search with name, email, member Id or referral code' />
                        <small>{this.state.searchMembers.length} results found</small>
                      </form>
                    </div>
                  </div>

                  <div>
                    {
                      this.state.members.length === 0 &&
                      <div className='my-5 py-5 d-flex flex-column align-items-center'>
                        <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                        There are no Members
                      </div>
                    }
                  </div>

                  {this.state.members.length > 0 &&
                    <>
                      <div className='table-responsive w-100'>
                        <table className='table table- primary'>
                          <thead>
                            <tr>
                              <th>
                                <input title="select all" onChange={async (e) => {
                                  // alert(e.target.checked)
                                  console.log(e.target.checked, e.target.defaultChecked)
                                  this.state.members.forEach(member => member.selected = !e.target.checked);
                                  this.setState({ members: this.state.members });
                                  // console.log(e.target.checked, e.target.defaultChecked, this.state.members.every(member => member.selected === true))
                                }} defaultChecked={this.state.members.every(member => member.selected === true)} className="form-check-input" type="checkbox" id='selectAllMembers' />
                                <label className="form-check-label ms-2" htmlFor='selectAllMembers' >#</label>
                              </th>
                              <th>Member</th>
                              <th>Slot&nbsp;Package</th>
                              <th>Rank</th>
                              <th>Email</th>
                              <th>Telephone</th>
                              <th>Registered</th>
                              <th>Status</th>
                              <th></th>
                            </tr>
                          </thead>

                          <tbody>
                            {this.state.viewMembers.slice(((this.state.membersPage * this.state.limit) - this.state.limit), (this.state.membersPage * this.state.limit)).map((member, index) => (
                              <tr key={index} >
                                <td className='align-middle'>
                                  <div className="form-check">
                                    <input onChange={() => this.selectMember(member)} checked={member.selected} className="form-check-input" type="checkbox" id={`member${index}`} />
                                    <label className="form-check-label" htmlFor={`member${index}`}>{index + 1 + (this.state.limit * (this.state.membersPage - 1))}</label>
                                  </div>
                                </td>
                                <td className='align-middle'>
                                  <div className='align-top'>
                                    <div className='d-flex'>
                                      <div className='me-2' onClick={() => { this.props.history.push('/admin/users/profile?u=' + member.email) }} style={{ cursor: 'pointer' }} title={`Click to view ${member.firstname ?? 'No name'}'s profile`}>
                                        <img id='profilePicturePreview' className='rounded-circle' width='50px' height='50px' src={member.profile_picture != null ? `${baseUrl}/${member.profile_picture}` : defaultProfilePicture} alt="" />
                                      </div>
                                      <div>
                                        {
                                          member.firstname === null && member.lastname === null && member.middlename === null
                                            ? <span>No&nbsp;name</span> :
                                            <span>{member.firstname}&nbsp;{member.lastname}&nbsp;{member.middlename}</span>
                                        } <br />
                                        <small>{member.member_id ?? <span>No&nbsp;member&nbsp;ID</span>}<span className='text-dark fw-bold mx-1'></span>{member.referral_code ?? 'No referral code'}</small>
                                      </div>
                                    </div>
                                  </div>
                                </td>
                                <td className='align-middle'>
                                  {member.slot_program} <br />
                                  <small>({member.no_slots}&nbsp;{member.no_slots > 1 ? 'slots' : 'slot'})</small>
                                </td>
                                <td className='align-middle'>
                                  {member.rank}
                                </td>
                                <td className='align-middle'>
                                  {member.email}
                                </td>
                                <td className='align-middle'>
                                  {member.telephone ?? <span>No&nbsp;telephone</span>}
                                </td>
                                <td className='align-middle'>
                                  {formatDate(member.created_at)}
                                </td>
                                <td className='align-middle'>
                                  <div style={{ width: '100px' }} className='d-flex align-items-center'>
                                    <div>{member.verification_status}</div>
                                    <FontAwesomeIcon style={{ cursor: 'pointer' }} onClick={() => {
                                      let editstatus = query(`#editstatus${index}`)[0];
                                      editstatus.hidden = !editstatus.hidden;
                                    }} className='text-primary ms-2' title={`Update ${member.firstname ?? 'No name'}'s status`} icon={faEdit}></FontAwesomeIcon>
                                  </div>

                                  <form id={`editstatus${index}`} hidden={true} onSubmit={(e) => this.updateStatus(e, member, index)}>
                                    <div className="form-floating mt-2">
                                      <select className="form-control" id={`status${index}`} defaultValue={member.verification_status}>
                                        <option value="verified">verified</option>
                                        <option value="blocked">blocked</option>
                                        <option value="closed">closed</option>
                                      </select>
                                      <label htmlFor={`status${index}`} className=''>select&nbsp;status</label>
                                      <button type='submit' className='btn mt-2 w-100 btn-sm btn-primary'>update</button>
                                    </div>
                                  </form>

                                </td>
                                <td className='align-middle'>
                                  <div className='d-flex'>
                                    <button onClick={() => this.sendSingleNotice(member.id, member.firstname)} className='btn btn-sm btn-muted px-' title={`Send notice to ${member.firstname ?? 'No name'}`}><FontAwesomeIcon className='text-primary' icon={faInfoCircle}></FontAwesomeIcon></button>

                                    <Link to={`/admin/users/downlines?email=${member.email}`} className='btn btn-sm btn-muted' title={`View ${member.firstname ?? 'No name'}'s downlines`}><FontAwesomeIcon className='text-primary' icon={faUsers}></FontAwesomeIcon></Link>

                                    <Link to={`/admin/users/slots?email=${member.email}`} className='btn btn-sm btn-muted' title={`View ${member.firstname ?? 'No name'}'s slots`}><FontAwesomeIcon className='text-primary' icon={faServer}></FontAwesomeIcon></Link>

                                    <Link to={`/admin/users/benefits?email=${member.email}`} className='btn btn-sm btn-muted' title={`View ${member.firstname ?? 'No name'}'s benefits`}><FontAwesomeIcon className='text-primary' icon={faCrown}></FontAwesomeIcon></Link>

                                    <button onClick={() => this.editPrivilege(member.id, member.firstname, member.role, member.permissions)} className='btn btn-sm btn-muted' title={`Edit ${member.firstname ?? 'No name'}'s privileges`}><FontAwesomeIcon className='text-primary' icon={faShieldAlt}></FontAwesomeIcon></button>
                                  </div>
                                </td>
                              </tr>
                            ))
                            }
                          </tbody>

                        </table>
                      </div>

                      <div className='my-2 d-flex justify-content-between align-items-center'>

                        <button disabled={this.state.membersPage === 1} className='btn btn-sm btn-primary' onClick={() => { this.setState({ membersPage: this.state.membersPage - 1 }) }}>&laquo; Previous</button>

                        <span>Page {this.state.membersPage} / {Math.ceil(this.state.viewMembers.length / this.state.limit)}</span>

                        <button disabled={(((this.state.membersPage + 1) * this.state.limit) - this.state.limit) > this.state.viewMembers.length} className='btn btn-sm btn-primary px-4' onClick={() => { this.setState({ membersPage: this.state.membersPage + 1 }) }}>Next &raquo;</button>

                      </div>

                      <hr />
                      <button disabled={
                        this.state.members.every((member) => { return member.selected === false })
                      } className='btn btn-sm btn-primary' onClick={() => this.sendNoticeToMany('Members')}>
                        <FontAwesomeIcon className='text-light' icon={faInfoCircle}></FontAwesomeIcon> Notify Selected Members
                      </button>

                    </>
                  }
                </div>

              </>
            }

            {this.editPrivilegeModal()}
            {this.sendNoticeModal()}

          </section>
        </Layout>
      </>
    );
  }

}

export default AdminUsers;